import { BROADCAST_SUCCESS, BROADCAST_FAIL, BROADCAST_DETAILS_SUCCESS, BROADCAST_DETAILS_FAIL } from "../actions/types";

const deafaultState = {};
deafaultState.broadcastmessagedata = null;

export default function (state = deafaultState, action) {
  const { type, payload } = action;

  switch (type) {
    case BROADCAST_SUCCESS:
      return {
        ...state,
        broadcastmessagedata: payload.broadcast,
      };
    case BROADCAST_FAIL:
      return {
        ...state,
        broadcastmessagedata: [],
      };
      case BROADCAST_DETAILS_SUCCESS:
        return {
          ...state,
          broadcastmessagedetailsdata:  payload.broadcastDetailsData,
        };
        case BROADCAST_DETAILS_FAIL:
        return {
          ...state,
          broadcastmessagedetailsdata: [],
        };
    default:
      return state;
  }
}
