import { CUG_SUCCESS, CUG_FAILURE } from "../actions/types";

const deafaultState = {};
deafaultState.cug = null;

export default function (state = deafaultState, action) {
  const { type, payload } = action;

  switch (type) {
    case CUG_SUCCESS:
      return {
        ...state,
        cug: payload.cug,
      };
    case CUG_FAILURE:
      return {
        ...state,
        cug: [],
      };
    default:
      return state;
  }
}
