import React, { useEffect, useState } from "react";
import ShowAllPolls from "./ShowAllPolls";
import { mergePoll } from "../../utils/defaultpoll";
import { Form } from "antd";
import { connect } from "react-redux";
import { getPollAsync } from "../../actions/poll";
import getCUG from "../../actions/cug";

const PollsContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [form] = Form.useForm();
  const [statusClicked, setStatusClicked] = useState(false);
  const [filter, setFilter] = useState({
    search: "",
    status_id: [],
    cug_filter: {},
  });
  const [checked, setChecked] = useState(false);
  const [totalPolls, setTotalPolls] = useState([]);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getPollAsync(filter));
    setLoader(true);
  }, [statusClicked, filter]);

  useEffect(() => {
    if (props.pollsdata === null) {
      setLoader(true);
    } else if (props.pollsdata !== null) {
      setLoader(false);
    }
    const polls = props.pollsdata !== null ? props.pollsdata : [];
    const isInactivePollPresent = filter?.status_id?.includes(4);
    if (
      filter?.search?.length > 0 ||
      (Object.values(filter?.status_id).length > 0 && !isInactivePollPresent)
    ) {
      setTotalPolls(polls);
    } else {
      setTotalPolls(mergePoll(polls));
    }
  }, [props.pollsdata]);

  const statusOnClicked = () => {
    setStatusClicked(!statusClicked);
  };

  const setFilterOptions = (type, value) => {
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
    setFilter(value);
  };

  const handleCheck = (data) => {
    setChecked(!checked);
    setFilter(data);
  };

  return (
    <ShowAllPolls
      polls={totalPolls}
      cug={props.cug}
      data={props}
      loader={loader}
      checked={checked}
      form={form}
      filter={filter}
      setLoader={setLoader}
      statusOnClicked={statusOnClicked}
      setFilterOptions={setFilterOptions}
      handleCheck={handleCheck}
    />
  );
};

function mapStateToProps(state) {
  const { pollsdata } = state.poll;
  const { cug } = state.cug;
  return {
    pollsdata,
    cug,
  };
}

export default connect(mapStateToProps)(PollsContainer);
