import React from "react";
import { Popover, Row, Col, Button, Divider } from "antd";
import "../hotlineMessages/FilterPopover.scss";
import { Scrollbars } from "react-custom-scrollbars";
import { FilterFilled, DownOutlined } from "@ant-design/icons";
import styles from "./managePoll.module.scss";
import MultiSelection from "./MultiSelection";
import { APPLY, CANCEL, FILTER, RESET, APPLY_FILTER } from "../../headings";
import { STATUS } from "../../labels";

const content = (props, hide, handleReset, reset, applyFilters) => (
  <div>
    <Col span={24} style={{ width: "325px" }}>
      <div className="">
        <Row className="filter-wrapper align-items-center px-3 py-2">
          <Col span="12">
            <h6>{FILTER}</h6>
          </Col>
          <Col span="12" className="d-flex justify-content-end">
            <Button
              type="link"
              className="link pl-0 pt-0"
              onClick={() => handleReset()}
            >
              {RESET}
            </Button>
          </Col>
        </Row>

        <Divider className="mt-0 mb-1 w-100" />
        <Scrollbars
          autoHeight
          autoHeightMin={0}
          autoHeightMax={450}
          thumbMinSize={30}
          universal={true}
        >
          <Row className="filter-wrapper align-items-center px-3 py-2">
            <div className="w-100 mb-4">
              <Col span="24" className="filter-type mb-3">
                <h6>{STATUS}</h6>
              </Col>
              <Col span="24">
                <MultiSelection
                  status
                  handlePollOnSelect={props.handlePollOnSelect}
                  reset={reset}
                />
                <DownOutlined className="down-arrow-ico" />
              </Col>
            </div>
            {props.cug &&
              Object.keys(props.cug).map((item) => {
                if (item.length > 0) {
                  return (
                    <div className="w-100 mb-4">
                      <Col span="24" className="filter-type mb-3">
                        <h6>
                          {item.substring(item.indexOf("_") + 1, item.length)}
                        </h6>
                      </Col>
                      <Col span="24">
                        <MultiSelection
                          cug
                          reset={reset}
                          cugData={props.cug}
                          item={item}
                          handlePollOnSelect={props.handlePollOnSelect}
                        />
                        <DownOutlined className="down-arrow-ico" />
                      </Col>
                    </div>
                  );
                }
              })}
          </Row>
        </Scrollbars>
      </div>
      <Row>
        <Col span="24" className="btn-row actions">
          <Button
            type="primary"
            className="mr-2"
            onClick={() => applyFilters()}
          >
            {APPLY}
          </Button>
          <Button type="secondary" onClick={() => hide()}>
            {CANCEL}
          </Button>
        </Col>
      </Row>
    </Col>
  </div>
);
class PollFilterPopover extends React.Component {
  state = {
    visible: false,
    reset: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible, reset: false });
  };

  handleReset = () => {
    this.setState({ reset: true, visible: false });
    this.props.reset();
  };

  applyFilters = () => {
    this.setState({
      visible: false,
    });
    this.props.applyFilters();
  };
  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(
            this.props,
            this.hide,
            this.handleReset,
            this.state.reset,
            this.applyFilters
          )}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <FilterFilled
            className={`${styles.filter_wrapper_ico}`}
            title={APPLY_FILTER}
          />
        </Popover>
      </>
    );
  }
}

export default PollFilterPopover;
