import React, { useState } from "react";
import { Row, Col, Form, Button, Select, Input, Alert } from "antd";
import styles from "../../components/managePolls/managePoll.module.scss";
import FloatLabel from "./FloatLabels";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "../../components/managePolls/MultipleChoice";
import authHeader from "../../services/auth-header";
import { CheckCircleTwoTone } from "@ant-design/icons";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";
import {
  DESCRIPTION,
  QUESTION,
  QUESTION_TYPE,
  RATING_TYPE,
} from "../../labels";
import { POLL_TITLE, NEXT, SAVE_AS_DRAFT_BTN } from "../../headings";
import {
  POLL_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
  ADD_OPTIONS,
  STAR_CHART_TYPE,
} from "../../errors";
import {
  LADDER,
  SELECT_MULTIPLE,
  SELECT_SINGLE,
  SMILEY,
  STARS,
  STAR_RATING,
  SELECT_SHORT_ANSWER,
  SELECT_LONG_ANSWER,
  THERMOMETER,
  THUMBSUP,
} from "../../values";
import { CREATE_POLL_STEP1_TEXT } from "../../messages";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  emptyIcon: <i class="far fa-smile"></i>,
  fullIcon: <i class="fa fa-smile"></i>,
  onChange: (newValue) => {},
};

const { TextArea } = Input;

const DuplicatePollStepOne = ({
  next,
  chartError,
  poll,
  setOptionsError,
  pollError,
  handlePollOnSelect,
  setSingleChoiceResponse,
  onCreateChange,
  setpollError,
  optionsError,
  form,
  location,
  createDuplicatePoll,
}) => {
  const [error, showError] = useState(false);

  const checkPollName = (e, type) => {
    if (poll.pollName === location.state.title) {
      setpollError("status: true");
    } else {
      onPreview(e, type);
    }
  };

  const onPreview = (e, type) => {
    e.preventDefault();
    if (
      poll.pollName &&
      poll.pollName.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollHelpText &&
      poll.pollHelpText.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollQuestionType &&
      poll.pollQuestionType.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      poll.pollQuestion
    ) {
      if (
        (poll.pollQuestionType === "1" || poll.pollQuestionType === "2") &&
        poll.pollResponse.length === 0
      ) {
        setOptionsError(true);
        showError(false);
      } else if (poll.pollQuestionType === "3") {
        if (poll.pollQuestionType === "3" && poll.pollRatingType.length === 0) {
          showError(true);
          setOptionsError(false);
        } else {
          showError(false);
          setOptionsError(false);
          if (type === "next") {
            next();
          } else {
            createDuplicatePoll();
          }
        }
      } else {
        showError(false);
        setOptionsError(false);
        if (type === "next") {
          next();
        } else {
          createDuplicatePoll();
        }
      }
    } else {
      showError(true);
      setOptionsError(false);
    }
  };

  const isPollNameDuplicate = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (e.target.value !== location.state.title) {
      axios
        .post(BASE_URL + "checkpoll/", { title: e.target.value }, options)
        .then((res) => {
          if (res.data) {
            setpollError(res.data[0]);
          }
        });
    } else {
      setpollError("status: true");
    }
  };

  return (
    <>
      <Col span={24}>
        <Form
          className="input-md floating-label w-100"
          name="validate_other"
          initialValues={{
            remember: true,
          }}
          form={form}
        >
          {error && (
            <Form.Item>
              <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
            </Form.Item>
          )}
          {optionsError && (
            <Form.Item>
              <Alert message={ADD_OPTIONS} type="error" showIcon></Alert>
            </Form.Item>
          )}
          <Col span={24}>
            <Row>
              <Col span={24} className="gutter-row">
                <FloatLabel label={POLL_TITLE} value={poll.pollName}>
                  <TextArea
                    showCount
                    maxLength={50}
                    htmlFor="polltitle"
                    value={poll.pollName}
                    id="pollName"
                    onChange={(e) => onCreateChange(e)}
                    onBlur={(e) => isPollNameDuplicate(e)}
                    autoComplete="off"
                    className={`required ${styles.sm_textarea}`}
                  />

                  {pollError === "status: false" &&
                    poll.pollName.replace(/^\s*/, "").replace(/\s*$/, "")
                      .length > 0 &&
                    poll.pollName.length > 0 && (
                      <CheckCircleTwoTone
                        className="successCheck"
                        twoToneColor="#52c41a"
                      />
                    )}
                  {pollError == "status: true" && (
                    <Alert
                      message={POLL_ALREADY_EXISTS}
                      type="error"
                      showIcon
                    ></Alert>
                  )}
                </FloatLabel>
              </Col>
              <Col span={24} className="gutter-row">
                <FloatLabel label={DESCRIPTION} value={poll.pollHelpText}>
                  <Form.Item
                    className="required"
                    name="pollHelpText"
                    id="pollHelpText"
                    validateStatus="success"
                  >
                    <>
                      <Input
                        className="input-control"
                        htmlFor="pollHelpText"
                        id="pollHelpText"
                        name="pollHelpText"
                        value={poll.pollHelpText}
                        autoComplete="off"
                        maxLength={500}
                        onChange={(e) => onCreateChange(e)}
                      />
                    </>
                  </Form.Item>
                </FloatLabel>
              </Col>

              <Col span={24}>
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 4 }}>
                  <Col span={24} lg={12} className="gutter-row">
                    <Col span={24} className="gutter-row">
                      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col
                          span={24}
                          md={10}
                          className="mb-3 t-2 mb-y-0 gutter-row"
                        >
                          <FloatLabel
                            label={QUESTION_TYPE}
                            value={poll.pollQuestionType}
                          >
                            <Form.Item
                              className="required mb-0"
                              name="questiontype"
                            >
                              <>
                                <Select
                                  className=""
                                  htmlFor="questiontype"
                                  className="required"
                                  id="pollQuestionType"
                                  value={poll.pollQuestionType}
                                  onSelect={(value) =>
                                    handlePollOnSelect(
                                      value,
                                      "pollQuestionType"
                                    )
                                  }
                                >
                                  <Select.Option value="1">
                                    {SELECT_SINGLE}
                                  </Select.Option>
                                  <Select.Option value="2">
                                    {SELECT_MULTIPLE}
                                  </Select.Option>
                                  <Select.Option value="3">
                                    {STAR_RATING}
                                  </Select.Option>
                                  <Select.Option value="4">
                                    {SELECT_SHORT_ANSWER}
                                  </Select.Option>
                                  <Select.Option value="5">
                                    {SELECT_LONG_ANSWER}
                                  </Select.Option>
                                </Select>
                              </>
                            </Form.Item>
                          </FloatLabel>
                        </Col>

                        {poll.pollQuestionType === "3" ? (
                          <Col span={24} md={10} lg={8} className="gutter-row">
                            <FloatLabel
                              label={RATING_TYPE}
                              value={poll.pollRatingType}
                            >
                              <Form.Item
                                className="required mb-0"
                                name="ratingtype"
                              >
                                <>
                                  <Select
                                    className=""
                                    htmlFor="ratingtype"
                                    className="required"
                                    id="pollRatingType"
                                    value={poll.pollRatingType}
                                    onSelect={(value) =>
                                      handlePollOnSelect(
                                        value,
                                        "pollRatingType"
                                      )
                                    }
                                  >
                                    <Select.Option value="1">
                                      {STARS}
                                    </Select.Option>
                                    <Select.Option value="2">
                                      {SMILEY}
                                    </Select.Option>
                                    <Select.Option value="3">
                                      {THERMOMETER}
                                    </Select.Option>
                                    <Select.Option value="4">
                                      {THUMBSUP}
                                    </Select.Option>
                                    <Select.Option value="5">
                                      {LADDER}
                                    </Select.Option>
                                  </Select>
                                </>
                              </Form.Item>
                            </FloatLabel>
                            {chartError && (
                              <Form.Item>
                                <Alert
                                  message={STAR_CHART_TYPE}
                                  type="error"
                                  showIcon
                                ></Alert>
                              </Form.Item>
                            )}
                          </Col>
                        ) : null}
                      </Row>
                    </Col>
                    <Col span={24} className="gutter-row">
                      <FloatLabel label={QUESTION} value={poll.pollQuestion}>
                        <Form.Item
                          className="required"
                          name="pollQuestion"
                          id="pollQuestion"
                          validateStatus="success"
                        >
                          <Input
                            className="input-control"
                            htmlFor="pollQuestion"
                            id="pollQuestion"
                            name="pollQuestion"
                            defaultValue={poll.pollQuestion}
                            value={poll.pollQuestion}
                            autoComplete="off"
                            maxLength={500}
                            onChange={(e) => onCreateChange(e)}
                          />
                        </Form.Item>
                      </FloatLabel>
                    </Col>
                  </Col>

                  <Col
                    span={24}
                    md={11}
                    className={`white-bg ml-5 p-4 ${styles.rating_img_wrapper}`}
                  >
                    {poll.pollQuestionType.length > 0 ? null : (
                      <div className="d-flex justify-content-center align-items-center">
                        {CREATE_POLL_STEP1_TEXT}
                      </div>
                    )}
                    {poll.pollQuestionType === "3" && poll.pollRatingType ? (
                      poll.pollRatingType === "1" ? (
                        <ReactStars
                          {...starsRating}
                          value={poll.pollRatingResponse}
                          name="pollRatingResponse"
                          onChange={(value) =>
                            handlePollOnSelect(value, "pollRatingResponse")
                          }
                          classNames="mt-3"
                        />
                      ) : poll.pollRatingType === "2" ? (
                        <img
                          className="font-size"
                          src={Smiley}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "3" ? (
                        <img
                          className="font-size"
                          src={Thermometer}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "4" ? (
                        <img
                          className="font-size"
                          src={Thumb}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "5" ? (
                        <img
                          className={`font-size ${styles.steps_img}`}
                          src={Steps}
                          alt="send message"
                        />
                      ) : null
                    ) : poll.pollQuestionType === "1" ? (
                      <div>
                        <MultiChoice
                          setSingleChoiceResponse={setSingleChoiceResponse}
                          pollResponse={poll.pollResponse}
                        />
                      </div>
                    ) : poll.pollQuestionType === "2" ? (
                      <MultiChoice
                        pollResponse={poll.pollResponse}
                        setSingleChoiceResponse={setSingleChoiceResponse}
                      />
                    ) : null}
                  </Col>
                  <Form.Item>
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="mr-4"
                      onClick={(e) => checkPollName(e, "next")}
                      data-test="stepOneNext"
                    >
                      {NEXT}
                    </Button>
                    <Button
                      type="secondary"
                      className="mr-4"
                      onClick={(e) => checkPollName(e, "draft")}
                    >
                      {SAVE_AS_DRAFT_BTN}
                    </Button>
                  </Form.Item>
                </Row>
              </Col>
            </Row>
          </Col>
        </Form>
      </Col>
    </>
  );
};

export default DuplicatePollStepOne;
