import React, { useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Breadcrumb,
  Button,
  Form,
  Input,
  DatePicker,
  Select,
  Alert,
  Spin,
  Checkbox,
} from "antd";
import { NavLink, useHistory } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import "react-phone-input-2/lib/style.css";
import OrgSidebar from "../../containers/OrgSidebar";
import styles from "./manageEmp.module.scss";
import "../form/form.scss";
import FloatLabel from "../form/FloatLabels";
import PhoneInput from "react-phone-input-2";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { MANAGE_EMPLOYEES, SAVE, CANCEL } from "../../headings";
import { EDIT_EMPLOYEE } from "../../messages";
import {
  REQUIRED_MESSAGE,
  SELECT_DATE,
  VALID_EMAIL,
  VALID_MOBILE,
} from "../../errors";
import {
  EMAIL,
  FIRST_NAME,
  LAST_NAME,
  MOBILE,
  STATUS,
  SELECT_ROLE,
  START_DATE,
  END_DATE,
} from "../../labels";
import {
  MAKE_SUPER_USER,
  MAKE_CONTROL_CENTER,
  MAKE_IDB_USER,
  MAKE_APP_USER,
  STATUS_ACTIVE,
  STATUS_INACTIVE,
} from "../../values";
import MultiRoleSelection from "./MultiRoleSelection";
import { SAVE_AS_DRAFT_BTN } from "../../headings";
import { DownOutlined } from "@ant-design/icons";

const { Content } = Layout;

const EditEmployee = (props) => {
  const [userDetails, setUserDetails] = useState({
    firstName: "",
    lastName: "",
    status: "",
    email: "",
    mobile: "",
    empId: "",
    department: "",
    startDate: "",
    endDate: "",
  });
  const [showRoles, setShowUserRoles] = useState(false);
  const [userRoles, setUserRoles] = useState([]);
  const history = useHistory();
  const [form] = Form.useForm();
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);
  const [emailValidationError, setEmailValidationError] = useState(false);
  const [mobileValidationError, setMobileValidationError] = useState(false);
  const [isChecked, setChecked] = useState(false);
  const [CUGFields, setCUGFields] = useState({});
  const [flexFields, setFlexFields] = useState({});
  const userId = props.computedMatch?.params.id;

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const roles = {
      2: history.location.state.is_org_cc_user ? "1" : "0",
      1: history.location.state.is_super_user ? "1" : "0",
      3: history.location.state.is_idb_user ? "1" : "0",
      0: "1",
    };
    setUserRoles(roles);
    setLoader(true);
    axios.get(BASE_URL + `employee/${userId}/`, options).then((res) => {
      const data = res.data;
      setUserDetails({
        ...userDetails,
        firstName: data.details.first_name,
        lastName: data.details.last_name,
        status: data.status_id.toString(),
        email: data.email,
        mobile: data.mobile_number,
        empId: "",
        department: "",
        startDate:
          data.details.start_date?.length > 0
            ? moment(data.details.start_date, "YYYY-MM-DD")
            : "",
        endDate:
          data.details.end_date?.length > 0
            ? moment(data.details.end_date, "YYYY-MM-DD")
            : "",
      });
      setLoader(false);
      form.setFieldsValue({
        firstName: data.details.first_name,
        lastName: data.details.last_name,
        email: data.email,
      });
      if (data.status_id === 2) {
        setChecked(true);
      } else {
        setChecked(false);
      }
      getCugFields(data.details);
    });
  }, []);

  const getCugFields = (data) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.get(BASE_URL + `getCUGFieldNames/`, options).then((res) => {
      let cug_Fields = {};

      let flex_Fields = {};
      if (Object.keys(res.data.cug_flex_fields).length !== 0) {
        let cug_data = Object.values(
          res.data.cug_flex_fields?.cug_fields
        ).reduce((accumulator, currentValue, currentIndex, array) => {
          cug_Fields = { ...cug_Fields, [currentValue]: null };
          return cug_Fields;
        }, []);
        cug_data = {
          Age: data.cug_field_2,
          Designation: data.cug_field_4,
          Gender: data.cug_field_1,
          Location: data.cug_field_3,
        };
        setCUGFields(cug_data);
        let flex_data = Object.values(
          res.data.cug_flex_fields?.flex_fields
        ).reduce((accumulator, currentValue) => {
          flex_Fields = { ...flex_Fields, [currentValue]: null };
          return flex_Fields;
        }, []);
        flex_data = {
          DOB: data.flex_field_2,
          Experience: data.flex_field_1,
          Identification: data.flex_field_4,
          Nationality: data.flex_field_3,
          Passport_no: data.flex_field_5,
        };
        setFlexFields(flex_data);
      }
    });
  };

  const handleUserOnChange = (e) => {
    setUserDetails({
      ...userDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleCUGFieldOnChange = (e, type) => {
    if (type === "cug") {
      setCUGFields({
        ...CUGFields,
        [e.target.id]: e.target.value,
      });
    }
    if (type === "flex") {
      setFlexFields({
        ...flexFields,
        [e.target.id]: e.target.value,
      });
    }
  };

  const handleMobile = (mobile) => {
    setUserDetails({
      ...userDetails,
      mobile: mobile,
    });
  };

  const handleUserOnSelect = (value, name) => {
    setUserDetails({ ...userDetails, [name]: value });
  };

  const handleUserDatePickerChange = (date, name) => {
    setUserDetails({ ...userDetails, [name]: date });
  };

  const showUserRoles = () => {
    setShowUserRoles(!showRoles);
  };

  const handleRoleOnSelect = (value, label, extra) => {
    const rolesArray = ["0", "1", "2", "3"];
    if (extra.triggerValue === "1" && extra.checked) {
      setUserRoles({
        0: "1",
        1: "1",
        2: "1",
        3: "1",
      });
    } else if (extra.triggerValue === "2" && !extra.checked) {
      let roles = value.filter((v) => v !== "1" && v !== "2");
      let roleData = {};
      Object.values(roles).map((role) => {
        roleData = {
          ...roleData,
          [role]: "1",
        };
      });
      rolesArray.map((key) => {
        if (!Object.keys(roleData).includes(key)) {
          roleData = {
            ...roleData,
            [key]: "0",
          };
        }
      });
      setUserRoles(roleData);
    } else if (extra.triggerValue === "3" && !extra.checked) {
      const roles = value.filter((v) => v !== "1" && v !== "3");
      let roleData = {};
      Object.values(roles).map((role) => {
        roleData = {
          ...roleData,
          [role]: "1",
        };
      });
      rolesArray.map((key) => {
        if (!Object.keys(roleData).includes(key)) {
          roleData = {
            ...roleData,
            [key]: "0",
          };
        }
      });
      setUserRoles(roleData);
    } else if (extra.triggerValue === "1" && !extra.checked) {
      setUserRoles({
        0: "1",
        1: "0",
        2: "1",
        3: "1",
      });
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailValidationError(true);
    } else {
      setEmailValidationError(false);
    }
  };

  const validateMobile = (e) => {
    var regex = /^\+\d$/;
    if (userDetails.mobile.length > 10 || regex.test(e.target.value)) {
      setMobileValidationError(false);
    } else {
      setMobileValidationError(true);
    }
  };

  const onUpdate = () => {
    updateEmpDetails();
  };

  const saveAsDraft = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    const data = {
      first_name: userDetails.firstName,
      last_name: userDetails.lastName,
      status_id: 2,
      is_org_cc_user:
        userRoles[MAKE_CONTROL_CENTER] === true ||
        userRoles[MAKE_CONTROL_CENTER] === "1"
          ? 1
          : 0,
      is_idb_user:
        userRoles[MAKE_IDB_USER] === true || userRoles[MAKE_IDB_USER] === "1"
          ? 1
          : 0,
      is_super_user:
        userRoles[MAKE_SUPER_USER] === true ||
        userRoles[MAKE_SUPER_USER] === "1"
          ? 1
          : 0,
      mobile_number: userDetails.mobile ? userDetails.mobile : 0,
      start_date: userDetails.startDate
        ? moment(userDetails.startDate).format("DD-MM-YYYY")
        : null,
      end_date: userDetails.endDate
        ? moment(userDetails.endDate).format("DD-MM-YYYY")
        : null,
    };
    axios.put(BASE_URL + `employee/${userId}/`, data, options).then(() => {
      setLoader(false);
      history.push("/orgcc/manageemployees");
    });
  };

  const updateEmpDetails = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    if (
      userDetails.startDate !== null &&
      userDetails.firstName.length > 0 &&
      userDetails.lastName.length > 0
    ) {
      showError(false);
      const data = {
        first_name: userDetails.firstName,
        last_name: userDetails.lastName,
        status_id: 1,
        is_org_cc_user:
          userRoles[MAKE_CONTROL_CENTER] === true ||
          userRoles[MAKE_CONTROL_CENTER] === "1"
            ? 1
            : 0,
        is_idb_user:
          userRoles[MAKE_IDB_USER] === true || userRoles[MAKE_IDB_USER] === "1"
            ? 1
            : 0,
        is_super_user:
          userRoles[MAKE_SUPER_USER] === true ||
          userRoles[MAKE_SUPER_USER] === "1"
            ? 1
            : 0,
        mobile_number: userDetails.mobile,
        start_date: userDetails.startDate
          ? moment(userDetails.startDate).format("DD-MM-YYYY")
          : null,
        end_date: userDetails.endDate
          ? moment(userDetails.endDate).format("DD-MM-YYYY")
          : null,
        cug_fields: CUGFields,
        flex_fields: flexFields,
      };
      axios.put(BASE_URL + `employee/${userId}/`, data, options).then(() => {
        setLoader(false);
        history.push("/orgcc/manageemployees");
      });
    } else {
      showError(true);
      setLoader(false);
    }
  };

  const onCancel = () => {
    history.push("/orgcc/manageemployees");
  };

  const onChangeCheckbox = (e) => {
    setChecked(e.target.checked);
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content className="w-100">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/manageemployees">
                      <Breadcrumb.Item>{MANAGE_EMPLOYEES}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{EDIT_EMPLOYEE}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{EDIT_EMPLOYEE}</h1>
                </Row>
                <div className="mid-content mt-4">
                  <Row className="d-flex justify-content-center align-items-center">
                    <Spin spinning={loader} size="large">
                      <Form
                        className="input-md floating-label w-100"
                        name=""
                        form={form}
                      >
                        {error && (
                          <Form.Item>
                            <Alert
                              message={REQUIRED_MESSAGE}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={FIRST_NAME}
                                value={userDetails.firstName}
                              >
                                <Form.Item
                                  className="required"
                                  name="firstName"
                                >
                                  <Input
                                    className="input-control"
                                    id="firstName"
                                    htmlFor="firstName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={LAST_NAME}
                                value={userDetails.lastName}
                              >
                                <Form.Item
                                  className="required"
                                  name="lastName"
                                  validateStatus="success"
                                >
                                  <Input
                                    className="input-control"
                                    id="lastName"
                                    htmlFor="lastName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={EMAIL}
                                value={userDetails.email}
                              >
                                <Form.Item
                                  className="required"
                                  name="email"
                                  validateStatus="success"
                                >
                                  <Input
                                    type="email"
                                    className="input-control"
                                    id="email"
                                    htmlFor="email"
                                    disabled={true}
                                    value={userDetails.email}
                                    onChange={(e) => handleUserOnChange(e)}
                                    onBlur={(e) => validateEmailAddress(e)}
                                    autoComplete="off"
                                  />
                                  {emailValidationError && (
                                    <Alert
                                      message={VALID_EMAIL}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col
                              span={24}
                              md={8}
                              className="gutter-row phone-field"
                            >
                              <FloatLabel
                                label={MOBILE}
                                value={userDetails.mobile}
                              >
                                <Form.Item
                                  className="required"
                                  name="mobile"
                                  validateStatus="success"
                                >
                                  <PhoneInput
                                    country="us"
                                    id="mobile"
                                    htmlFor="mobile"
                                    value={userDetails.mobile}
                                    onChange={handleMobile}
                                    onBlur={validateMobile}
                                    autoComplete="off"
                                  />
                                  {mobileValidationError && (
                                    <Alert
                                      message={VALID_MOBILE}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            {/* <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={STATUS}
                                value={userDetails.status}
                              >
                                <Form.Item className="required" name="status">
                                  <>
                                    <Select
                                      className="input-control w-100"
                                      value={userDetails.status}
                                      htmlFor="status"
                                      onSelect={(value) =>
                                        handleUserOnSelect(value, "status")
                                      }
                                    >
                                      <Option value="1">{STATUS_ACTIVE}</Option>
                                      <Option value="2">
                                        {STATUS_INACTIVE}
                                      </Option>
                                    </Select>
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col> */}
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={START_DATE} value="date">
                                <Form.Item
                                  className="input-control datepicker-wrapper required"
                                  rules={[
                                    {
                                      required: true,
                                      message: SELECT_DATE,
                                    },
                                  ]}
                                >
                                  <DatePicker
                                    className="w-100 "
                                    name="startDate"
                                    value={userDetails.startDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(
                                        date,
                                        "startDate"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={END_DATE} value="date">
                                <Form.Item className="input-control datepicker-wrapper">
                                  <DatePicker
                                    className="w-100 "
                                    name="endDate"
                                    value={userDetails.endDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(
                                        date,
                                        "endDate"
                                      )
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <MultiRoleSelection
                                handleRoleOnSelect={handleRoleOnSelect}
                                roles={userRoles}
                                viaEdit
                              />
                              <DownOutlined className="down-arrow-ico" />
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row className="py-3">
                            <span className="mb-0">CUG Fields</span>
                          </Row>

                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            {Object.keys(CUGFields).map((c) => (
                              <Col span={24} md={8} className="gutter-row">
                                <FloatLabel label={c} value={CUGFields[c]}>
                                  <Form.Item name={c} validateStatus="success">
                                    <Input
                                      className="input-control"
                                      id={c}
                                      defaultValue={CUGFields[c]}
                                      value={CUGFields[c]}
                                      htmlFor={c}
                                      onChange={(e) =>
                                        handleCUGFieldOnChange(e, "cug")
                                      }
                                      autoComplete="off"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            ))}
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row className="py-3">
                            <span className="mb-0">Flex fields</span>
                          </Row>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            {Object.keys(flexFields).map((f) => (
                              <Col span={24} md={8} className="gutter-row">
                                <FloatLabel label={f} value={flexFields[f]}>
                                  <Form.Item name={f} validateStatus="success">
                                    <Input
                                      className="input-control"
                                      id={f}
                                      defaultValue={flexFields[f]}
                                      value={flexFields[f]}
                                      htmlFor={f}
                                      onChange={(e) =>
                                        handleCUGFieldOnChange(e, "flex")
                                      }
                                      autoComplete="off"
                                    />
                                  </Form.Item>
                                </FloatLabel>
                              </Col>
                            ))}
                          </Row>
                        </Col>

                        {/* <Col
                          span={24}
                          md={8}
                          className="gutter-row phone-field"
                        >
                           <Form.Item className=""
                              name="mobile"
                              validateStatus="success">
                                  <Checkbox onChange={(e) => onChangeCheckbox(e)}
                                  value={isChecked}
                                  >
                                   is Draft ?
                                  </Checkbox>
                            </Form.Item>
                        </Col> */}
                        <Form.Item className="mt-5">
                          <Button
                            type="primary"
                            htmlType="submit"
                            className="mr-4"
                            onClick={onUpdate}
                          >
                            {SAVE}
                          </Button>
                          <Button
                            type="secondary"
                            className="mr-4"
                            onClick={saveAsDraft}
                          >
                            {SAVE_AS_DRAFT_BTN}
                          </Button>
                          <Button type="secondary" onClick={onCancel}>
                            {CANCEL}
                          </Button>
                        </Form.Item>
                      </Form>
                    </Spin>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};
export default EditEmployee;
