import React from "react";
import { Layout, Row, Col, Input, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import { AudioOutlined, PaperClipOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import IdbSidebar from "../reports/IdbSidebar";
import moment from "moment";
import { MANAGE_POLLS, SEARCH } from "../../headings";
import {
  COMPLETED,
  SCHEDULED,
  STATUS_ACTIVE,
  STATUS_INACTIVE,
} from "../../values";
import PollFilterPopover from "./PollFilter";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllPollResults extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      cug: {},
      filterData: {},
    };
  }

  componentDidMount() {
    let { polls, cug, filter } = this.props;
    polls?.forEach((poll) => {
      poll.start_date = moment(poll.start_date)
        .utc()
        .local()
        .format("MM/DD/YYYY");
      poll.end_date = moment(poll.start_date).isValid()
        ? moment(poll.end_date).utc().local().format("MM/DD/YYYY")
        : "";
    });
    this.setState({ data: polls, cug: cug?.cug_field, filterData: filter });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.polls !== nextProps.polls) {
      nextProps.polls?.forEach((poll) => {
        poll.start_date = moment(poll.start_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
        poll.end_date = moment(poll.end_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
      });
      this.setState({ data: nextProps.polls });
    }
    if (this.props.cug !== nextProps.cug) {
      this.setState({ cug: nextProps.cug?.cug_field });
    }
    if (this.props.filter !== nextProps.filter) {
      this.setState({ filterData: nextProps.filter });
    }
  }

  onSearch = (value) => {
    const data = { ...this.state.filterData, search: value };
    this.setState({ filterData: data });
    this.props.setFilterOptions(data);
  };

  applyFilters = () => {
    this.props.setFilterOptions(this.state.filterData);
  };

  reset = () => {
    this.setState({ filterData: {} });
    this.props.setFilterOptions({ status_id: [], cug_filter: {} });
  };

  handlePollOnSelect = (value, type, item) => {
    var newArray = value.map((item) => {
      if (type === "status_id") {
        return parseInt(item);
      } else {
        return item;
      }
    });
    let data = {};
    if (item) {
      if (value.length === 0) {
        const cugData = this.state.filterData.cug_filter;
        const filteredCugData = Object.keys(cugData)
          .filter((key) => key !== item)
          .reduce((obj, key) => {
            obj[key] = cugData[key];
            return obj;
          }, {});
        data = {
          ...this.state.filterData,
          [type]: {
            ...filteredCugData,
          },
        };
      } else {
        data = {
          ...this.state.filterData,
          [type]: {
            ...this.state.filterData[type],
            [item]: newArray,
          },
        };
      }
    } else {
      data = { ...this.state.filterData, [type]: newArray };
    }
    this.setState({ filterData: data });
  };

  handleCheck = (e) => {
    let data = {};
    if (e.target.checked) {
      data = {
        status_id: [5, 4, 7, 8],
      };
    } else {
      data = {
        status_id: [5, 7],
      };
    }
    this.props.handleCheck(data);
  };

  render() {
    const { loader } = this.props;
    const columns = [
      {
        title: "",
        dataIndex: "",
        key: "name",
        width: "5%",
        render: (record) => {
          return {
            children:
              record.image || record.document ? <PaperClipOutlined /> : null,
            props: {
              "data-title": "",
            },
          };
        },
      },
      {
        title: "Poll Name",
        dataIndex: "title",
        key: "name",
        width: "22%",
        render: (name, record) => {
          return {
            children:
              record.poll_id != 2020202020 && record.poll_id != 1010101010 ? (
                <Link to={{ pathname: "/idb/pollresponse", state: record }}>
                  <li>
                    <a href={`/idb/pollresponse`}>{name}</a>{" "}
                  </li>
                </Link>
              ) : (
                name
              ),
            props: {
              "data-title": "Poll Name",
            },
          };
        },
      },
      {
        title: "Start Date & Time",
        dataIndex: "",
        key: "start_date",
        width: "22%",
        render: (record) => {
          return {
            children: record.start_date + " " + record.start_time.slice(0, -3),
            props: {
              "data-title": "Start Date & Time",
            },
          };
        },
      },
      {
        title: "End Date & Time",
        dataIndex: "",
        key: "end_date",
        width: "21%",
        render: (record) => {
          return {
            children: record.end_date + " " + record.end_time.slice(0, -3),
            props: {
              "data-title": "Start Date & Time",
            },
          };
        },
      },
      {
        title: "Created By",
        dataIndex: "",
        key: "end_date",
        width: "21%",
        render: (record) => {
          return {
            children:
              record.poll_id != 1010101010 && record.poll_id != 2020202020
                ? record.first_name + " " + record.last_name
                : "",
            props: {
              "data-title": "Created By",
            },
          };
        },
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.poll_id != 1010101010 && record.poll_id != 2020202020 ? (
                record.status_id === 4 ? (
                  <div className="secondary-tag-rounded tag">
                    {STATUS_INACTIVE}
                  </div>
                ) : record.status_id === 5 ? (
                  <div className="primary-tag-rounded tag">
                    <span className="circle mr-1"></span>
                    {STATUS_ACTIVE}
                  </div>
                ) : record.status_id === 7 ? (
                  <div className="primary-tag-rounded tag">
                    <span className="circle mr-1"></span>
                    {SCHEDULED}
                  </div>
                ) : (
                  <div className="primary-tag-rounded tag">
                    <span className="circle mr-1"></span>
                    {COMPLETED}
                  </div>
                )
              ) : (
                <div className="secondary-tag-rounded tag">{"Sample"}</div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
    ];

    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amIDBUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <IdbSidebar amIDBUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>{MANAGE_POLLS}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder={SEARCH}
                            onSearch={this.onSearch}
                          />
                        </Col>
                        <Col
                          span={24}
                          lg={9}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <PollFilterPopover
                            handlePollOnSelect={this.handlePollOnSelect}
                            cug={this.state.cug}
                            applyFilters={this.applyFilters}
                            reset={this.reset}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={loader} size="large">
                  {/* <Checkbox onChange={(e) => this.handleCheck(e)}>
                    {SHOW_ALL_POLLS}
                  </Checkbox> */}
                  <Grid
                    rowKey="poll_id"
                    columns={columns}
                    data={this.state.data}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllPollResults;
