import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Button,
  Input,
  Select,
  Divider,
  Upload,
  Image,
  Spin,
  Typography,
  Modal
} from "antd";
import { Link } from "react-router-dom";
import ReactHtmlParser from "react-html-parser";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import BroadcastPopover from "../table/BroadcastPopover";
import {SHOW_LESS} from "../../headings";
import Location from "./Location";
import MapContainer from "./GMap";
import "../modal/modal.scss";
import ShowMoreText from 'react-show-more-text';
import PollFilterPopover from "../managePolls/PollFilter";
import getCUG from "../../actions/cug";

import moment from "moment";
const fileobj = [
  {
    uid: "-1",
    name: "image.png",
    status: "done",
    url:
      "https://amsstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg",
    thumbUrl:
      "https://amstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg",
  },
];

const { Content } = Layout;
const { Paragraph } = Typography;

const { Search } = Input;
const BroadcastMessages = ({
  setFilterOptions,
  setSearchValue,
  setSortOptions,
  data,
  broadcastmessages,
  loader,
  cug
}) => {
  const [expand, setExpand] = useState(false);
  const [counter, setCounter] =useState (0);
  const [inputSearchValue, setinputSearchValue] = useState("");
  const [visibleModal, setvisibleModal] = useState(false);
  const [locationOnBroadCastList, setlocationOnBroadCastList] = useState("");
  const [selectedlongitude, setselectedlongitude] = useState("");
  const [selectedlatitude, setselectedlatitude] = useState("");
  const [selectedStatus, setSelectedStatus] = useState([]);
  const [cug_fliterData, setCug_fliterData] = useState({});

  const [filterData, setfilterData] = useState({
        "status_id":[],
        "sort":"",
        "cug_fliter":{}
    });


  const handleFilterOnSelect = (value) => {
    let newArray = [];
    if (value) {
      newArray = [parseInt(value)];
    }
    const data = {
      status_id: newArray,
    };
    setFilterOptions(data);
  };
  const onSearch = (value) => {
    const consolidatedObj = {
      status_id: selectedStatus,
      cug_fliter: cug_fliterData,
      search:value
    }
    setFilterOptions(consolidatedObj);
    //setSearchValue(value);
    setinputSearchValue(value);
  };

  const onSort = (value) => {
    //setSortOptions(value);
    const consolidatedObj = {
      status_id: selectedStatus,
      cug_fliter: cug_fliterData,
      sort:value
    }
    setFilterOptions(consolidatedObj );
  };

  const boldSubstring = (str, inputSearchValue) => {
    if(inputSearchValue){
      return str.replace(new RegExp(inputSearchValue, 'gi'), match => `<mark>${match}</mark>`);
    }else{
      return str;
    }
  };

  const setLocation = (latitude, longitude, name) => {
      console.log(latitude, longitude, name);
  } 

  const renderParagraph = (item) =>{
    return (
      <div className="note-wrapper m-2" key={counter}>
          {ReactHtmlParser(boldSubstring(item, inputSearchValue))}
      </div>
    );

  }

  const typoExpand = (value)=>{
    setExpand(true);
    if(!expand){
      setCounter(counter+0);
    }else{
      setCounter(counter+1);
    }
  }

  const typoClose = (value)=>{
    setExpand(false)
    if(!expand){
      setCounter(counter+0);
    }else{
      setCounter(counter+1);
    }
    console.log("on close")
  }

  const showModal = (lat, lng, address)=>{

  setlocationOnBroadCastList(address);
  setselectedlongitude(lng);
  setselectedlatitude(lat)
  setvisibleModal(true);

  }

  const handlePollOnSelect =  (value, type, item)=>{
    console.log(value, type, item);
    if(type == "status_id"){
      //filterData, setfilterData
      const filterDataObject = filterData;
      filterDataObject["status_id"] = value;
      setSelectedStatus(value);
      setfilterData(filterDataObject);
    }
    if(type == "cug_filter"){
      const cugFieldName = item.split("_");
      const fieldName = cugFieldName[1];
      let existingCUGValue = cug_fliterData;
      existingCUGValue[fieldName] = value;
      if(value.value > 0){
      setCug_fliterData(existingCUGValue);
      }else if(existingCUGValue && existingCUGValue[fieldName] && value.value > 0){
        delete existingCUGValue[fieldName];
        setCug_fliterData(existingCUGValue);
      }

      let filterDataObject = filterData;
      filterDataObject["cug_fliter"] = existingCUGValue;
      setfilterData(filterDataObject);
      setCug_fliterData(existingCUGValue);
  } 
}
  const applyFilters = () =>{
    const consolidatedObj = {
      status_id: selectedStatus,
      cug_fliter: cug_fliterData
    }
    setFilterOptions(consolidatedObj );
    
  } 
  const reset = () =>{
    setSelectedStatus([]);
    setCug_fliterData({});
    setCug_fliterData({
      "status_id":[],
      "sort":"",
      "search":"",
      "cug_fliter":{}
  });

    setFilterOptions({
      "status_id":[],
      "sort":"",
      "search":"",
      "cug_fliter":{}
  });
    console.log("reset filter ");
  }

  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={data} />
            <Content>
              <Row className="py-3 justify-content-between">
                <h1 className="mb-0">Broadcast Messages</h1>
              </Row>
              <Col span={24}>
                <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                  <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                    <Row>
                      <Col span={24} lg={9} className="search-box">
                        <Search placeholder="Search" onSearch={onSearch} />
                      </Col>
                      <Col
                        span={24}
                        lg={6}
                        className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                      >
                        {/* <Select
                          className="w-100 w-md-75"
                          defaultValue="Filter"
                          onSelect={(value) => handleFilterOnSelect(value)}
                        >
                          <Option value="">All</Option>
                          <Option value="7">Scheduled</Option>
                          <Option value="9">Published</Option>
                        </Select> */}
                          <PollFilterPopover
                            handlePollOnSelect={handlePollOnSelect}
                            cug={cug}
                            applyFilters={applyFilters}
                            reset={reset}
                          />
                      </Col>
                      <Col
                        span={24}
                        lg={6}
                        className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                      >
                        <Select
                          className="w-100 w-md-75"
                          defaultValue="Sort"
                          onSelect={(value) => onSort(value)}
                        >
                          <Option value="DESC">Newest at top</Option>
                          <Option value="ASC">Oldest at top</Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>

                  <Col
                    span={24}
                    lg={6}
                    className="d-flex align-items-center justify-content-end"
                  >
                    <Link to="/orgcc/createbroadcast">
                      <Button type="primary">Create Broadcast</Button>
                    </Link>
                  </Col>
                </Row>
              </Col>
              <Spin spinning={loader} size="large">
                <div className="mid-content">
                  {broadcastmessages?.map((value) => {
                    const getAllKeys = Object.keys(value);
                    const matchingKeys = getAllKeys.filter((key) => key.includes('cugField'));
                    let cugData = "";
                    matchingKeys.forEach((element)=>{
                       const cugDataValues =  value[element];
                       const slitFieldName = element.split("_");
                       cugData += cugDataValues && cugDataValues.length > 0 ? ` ${slitFieldName[1]} : ${cugDataValues.toString()},   `:"";
                    })
                     return (
                    <Row>
                      <Col span={22}>
                        <h6 className="font-weight-md mb-1">{ReactHtmlParser(boldSubstring(value.title, inputSearchValue))}</h6>
                        <div>
                        <small>
                          {!cugData ? "" : "CUG Parameters - " }
                        </small>
                        <small>
                          <i>
                          {cugData ? cugData : "" }
                          </i>
                        </small>
                        </div>
                        <div>
                        <small>
                          {" Time Zone - " }
                        </small>
                        <small>
                          <i>
                          {value ? value.scheduled_date_timezone : "" }
                          </i>
                        </small>
                        </div>
                        <div style={{paddingBottom:8}}>
                        {value.status_id === "7" ? (
                          <small>
                            {"Scheduled on: "}
                            {moment(value.scheduled_date)
                              .utc()
                              .local()
                              .format("MM/DD/YYYY")}
                          </small>
                        ) : (
                          <small>
                            {"Published on: "}
                            {moment(value.scheduled_date)
                              .utc()
                              .local()
                              .format("MM/DD/YYYY")}
                          </small>
                        )}
                        </div>
                        <div style={{paddingBottom:8}} >
                         <ShowMoreText
                            lines={3}
                            more='Show more'
                            less='Show less'
                            className='content-css'
                         >
                            {renderParagraph(value.message)}
                         </ShowMoreText>
                         </div>
                         {value.address != null && value.location_latitude !=null
                          ? (
                              <div className="attachment-wrapper">
                                <span className="icon icon-location pr-1" />
                                <a className="text-truncate w-75" onClick={()=>{ showModal(value.location_latitude, value.location_longitude, value.address)}}>
                                   {value.address}
                                </a>

                                  {/* <Button
                                  type="link"
                                  className="upload-list link pl-0"
                                  onClick={showModal(value.location_latitude, value.location_longitude, value.address)}
                                  >
                                  <span className="icon icon-location"></span>
                                  {value.address}
                                  </Button> */}

                                {/* <Location
                                      setLocation={setLocation}
                                      longitude={value.location_longitude}
                                      latitude={value.location_latitude}
                                      locationName={
                                        value.address
                                      }
                                      isEdit={false}
                                      locationOnBroadCastList={value.address}
                                    /> */}
                              </div>
                            )
                          : null}
                        {/* <p className="pt-2">{ReactHtmlParser(value.message)}</p> */}
                        {value.broadcast_images != null
                          ? value.broadcast_images?.map((item) => (
                              <Image
                                width={100}
                                src={item.url}
                                className="pr-3"
                                placeholder={item.name}
                                alt={item.name}
                              />
                            ))
                          : null}
                          
                        {value.broadcast_documents != null
                          ? value.broadcast_documents?.map((item) => (
                              <div className="attachment-wrapper">
                                <span className="icon icon-doc pr-2" />
                                <a href={item.url} target="_blank">
                                  {item.name}
                                </a>
                              </div>
                            ))
                          : null}
                      </Col>
                      <Col
                        span={2}
                        className="d-flex justify-content-end align-items-start"
                      >
                        <BroadcastPopover data={value} />
                      </Col>
                      <Divider className="w-100 mt-2" />
                    </Row>
                  )})}
                </div>
              </Spin>
                  <Modal
                  title={"Selected Location"}
                  visible={visibleModal}
                  onCancel={() =>setvisibleModal(false)}
                  centered
                  width={800}
                  okButtonProps={{
                    style: { display: "none"},
                  }}
                  >
                  <Row className="py-4 map-container">
                  <MapContainer
                    getMapLocation={""}
                    getCurrenLocationName={""}
                    longitude={selectedlongitude}
                    latitude={selectedlatitude}
                    viaModal={""}
                    locationOnBroadCastList = {locationOnBroadCastList}
                  />
                  </Row>
                  </Modal>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

export default BroadcastMessages;
