import React from "react";
import {
  Row,
  Col,
  Form,
  Button,
  Select,
  Input,
  Switch,
  Upload,
  Modal,
  DatePicker,
} from "antd";
import styles from "../../components/managePolls/managePoll.module.scss";
import FloatLabel from "./FloatLabels";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "../../components/managePolls/MultipleChoice";
import MultiSelect from "../managePolls/MultiSelect";
import PieChart from "../../assets/images/pie-chart.svg";
import BarChart from "../../assets/images/bar-chart.svg";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import "../modal/modal.scss";
import "date-fns";
import {
  PUBLISH,
  SCHEDULE,
  CONFIGURE_CUG,
  BACK,
  CLOSE_TEXT_MODAL,
  POLL_TITLE,
  UPLOAD,
} from "../../headings";
import {
  DESCRIPTION,
  QUESTION,
  QUESTION_TYPE,
  RATING_TYPE,
  SHOW_RESULTS_POLL,
  SELECT_START_DATE,
  SELECT_END_DATE,
  TIME_ZONE,
} from "../../labels";
import {
  LADDER,
  SELECT_MULTIPLE,
  SELECT_SINGLE,
  SMILEY,
  STARS,
  STAR_RATING,
  THERMOMETER,
  THUMBSUP,
} from "../../values";
import ShowChoices from "../managePolls/ShowChoices";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  emptyIcon: <i class="far fa-smile"></i>,
  fullIcon: <i class="fa fa-smile"></i>,
  onChange: (newValue) => {},
};

const { TextArea } = Input;

const CreatePollStepThree = ({
  poll,
  prev,
  handleCancel,
  handlePollImagePreview,
  createPoll,
}) => {
  let text = "";
  if (poll.cugDetails) {
    Object.values(poll.cugDetails).some(function (key) {
      if (key.length > 0) {
        text = CONFIGURE_CUG;
      }
    });
  }
  return (
    <>
      <Col span={24}>
        <Form className="input-md floating-label w-100" name="validate_other">
          <Col span={24}>
            <Row>
              <Col span={24} className="gutter-row">
                <FloatLabel label={POLL_TITLE} value={poll.pollName}>
                  <Form.Item
                    className="required"
                    name="polltitle"
                    id="polltitle"
                  >
                    <>
                      <TextArea
                        htmlFor="polltitle"
                        value={poll.pollName}
                        id="polltitle"
                        name="polltitle"
                        disabled
                      />
                    </>
                  </Form.Item>
                </FloatLabel>
              </Col>
              <Col span={24} className="gutter-row">
                <FloatLabel label={DESCRIPTION} value={poll.pollHelpText}>
                  <Form.Item
                    className="required"
                    name="pollHelpText"
                    id="pollHelpText"
                  >
                    <>
                      <Input
                        className="input-control"
                        htmlFor="pollHelpText"
                        id="pollHelpText"
                        name="pollHelpText"
                        value={poll.pollHelpText}
                        disabled
                      />
                    </>
                  </Form.Item>
                </FloatLabel>
              </Col>

              <Col span={24}>
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 4 }}>
                  <Col span={24} lg={12} className="gutter-row">
                    <Col span={24} className="gutter-row">
                      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col
                          span={24}
                          md={10}
                          className="mb-3 t-2 mb-y-0 gutter-row"
                        >
                          <FloatLabel
                            label={QUESTION_TYPE}
                            value={poll.pollQuestionType}
                          >
                            <Form.Item
                              className="required mb-0"
                              name="pollQuestionType"
                            >
                              <>
                                <Select
                                  className=""
                                  htmlFor="pollQuestionType"
                                  className="required"
                                  id="pollQuestionType"
                                  value={poll.pollQuestionType}
                                  disabled
                                >
                                  <Select.Option
                                    value="1"
                                    disabled={
                                      poll.pollChartType !== "" &&
                                      poll.pollChartType === "4"
                                    }
                                  >
                                    {SELECT_SINGLE}
                                  </Select.Option>
                                  <Select.Option
                                    value="2"
                                    disabled={
                                      poll.pollChartType !== "" &&
                                      poll.pollChartType === "4"
                                    }
                                  >
                                    {SELECT_MULTIPLE}
                                  </Select.Option>
                                  <Select.Option
                                    value="3"
                                    disabled={
                                      poll.pollChartType !== "" &&
                                      poll.pollChartType !== "4"
                                    }
                                  >
                                    {STAR_RATING}
                                  </Select.Option>
                                </Select>
                              </>
                            </Form.Item>
                          </FloatLabel>
                        </Col>

                        {poll.pollQuestionType === "3" ? (
                          <Col span={24} md={10} lg={8} className="gutter-row">
                            <FloatLabel
                              label={RATING_TYPE}
                              value={poll.pollRatingType}
                            >
                              <Form.Item
                                className="required mb-0"
                                name="ratingtype"
                              >
                                <>
                                  <Select
                                    className=""
                                    htmlFor="ratingtype"
                                    className="required"
                                    id="ratingtype"
                                    value={poll.pollRatingType}
                                    disabled
                                  >
                                    <Select.Option value="1">
                                      {STARS}
                                    </Select.Option>
                                    <Select.Option value="2">
                                      {SMILEY}
                                    </Select.Option>
                                    <Select.Option value="3">
                                      {THERMOMETER}
                                    </Select.Option>
                                    <Select.Option value="4">
                                      {THUMBSUP}
                                    </Select.Option>
                                    <Select.Option value="5">
                                      {LADDER}
                                    </Select.Option>
                                  </Select>
                                </>
                              </Form.Item>
                            </FloatLabel>
                          </Col>
                        ) : null}
                      </Row>
                    </Col>
                    <Col span={24} className="gutter-row">
                      <FloatLabel label={QUESTION} value={poll.pollQuestion}>
                        <Form.Item
                          className="required"
                          name="pollQuestion"
                          id="pollQuestion"
                          validateStatus="success"
                        >
                          <Input
                            className="input-control"
                            htmlFor="pollQuestion"
                            id="pollQuestion"
                            name="pollQuestion"
                            defaultValue={poll.pollQuestion}
                            value={poll.pollQuestion}
                            disabled
                          />
                        </Form.Item>
                      </FloatLabel>
                    </Col>
                  </Col>

                  <Col
                    span={24}
                    md={11}
                    className={`white-bg ml-5 p-4 ${styles.rating_img_wrapper}`}
                  >
                    {poll.pollQuestionType === "3" && poll.pollRatingType ? (
                      poll.pollRatingType === "1" ? (
                        <ReactStars
                          {...starsRating}
                          value={poll.pollRatingResponse}
                          name="pollRatingResponse"
                          onChange={(value) =>
                            handlePollOnSelect(value, "pollRatingResponse")
                          }
                          classNames="mt-3"
                        />
                      ) : poll.pollRatingType === "2" ? (
                        <img
                          className="font-size"
                          src={Smiley}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "3" ? (
                        <img
                          className="font-size"
                          src={Thermometer}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "4" ? (
                        <img
                          className="font-size"
                          src={Thumb}
                          alt="send message"
                        />
                      ) : poll.pollRatingType === "5" ? (
                        <img
                          className={`font-size ${styles.steps_img}`}
                          src={Steps}
                          alt="send message"
                        />
                      ) : null
                    ) : poll.pollQuestionType === "1" ||
                      poll.pollQuestionType === "2" ? (
                      <div>
                        <ShowChoices
                          pollResponse={poll.pollResponse}
                          questionType={poll.pollQuestionType}
                        />
                      </div>
                    ) : null}
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
          <Col span={24}>
            {poll.cugDetails && Object.keys(poll.cugDetails).length > 0 ? (
              <>
                {text.length > 0 ? <h6 className="mb-4">{text}</h6> : null}
                <Row>
                  {Object.entries(poll.cugDetails).map((value) => {
                    if (Object.values(value[1])?.length > 0) {
                      return (
                        <>
                          <Col span={24} lg={8}>
                            <label>
                              {value[0].substring(
                                value[0].indexOf("_") + 1,
                                value[0].length
                              )}
                            </label>
                            <Col
                              span={24}
                              className="tags-wrapper tag-view align-items-center"
                            >
                              <MultiSelect
                                pollCug={poll.cugDetails}
                                item={value[0]}
                                viaStep3
                              ></MultiSelect>
                            </Col>
                          </Col>
                        </>
                      );
                    }
                  })}
                </Row>
              </>
            ) : null}
            {poll.pollDocument.length > 0 ? (
              <Col span={24} className="gutter-row mt-5">
                <h6>{UPLOAD}</h6>
                <Form.Item name="upload" className="upload-list">
                  <Upload
                    listType="picture"
                    fileList={poll.pollDocument}
                    onPreview={(value) => handlePollImagePreview(value)}
                  ></Upload>
                  <Modal
                    visible={poll.previewVisible}
                    title={poll.previewTitle}
                    centered
                    onOk={(value) => handleCancel(value)}
                    okButtonProps={{
                      children: "Custom OK",
                    }}
                    cancelButtonProps={{
                      style: { display: "none" },
                    }}
                    okText={CLOSE_TEXT_MODAL}
                  >
                    <img
                      alt="image"
                      style={{ width: "100%" }}
                      src={poll.previewImage}
                    />
                  </Modal>
                </Form.Item>
              </Col>
            ) : null}
            <Col span={24} className="gutter-row mt-5">
              <Row>
                <Col span={24} md={8} lg={8} xl={6} className="mb-3 t-2 mb-y-0">
                  <Form.Item
                    name="showresult"
                    label={SHOW_RESULTS_POLL}
                    className="mb-0"
                  >
                    <Switch
                      id="showResult"
                      checked={poll.showResult}
                      disabled
                    />
                  </Form.Item>
                </Col>
                {poll.showResult ? (
                  <Col span={24}>
                    <Col span={12}>
                      {poll.switch ? (
                        <img className="w-100" src={PieChart} alt="Pie Chart" />
                      ) : (
                        <img className="w-100" src={BarChart} alt="Bar Chart" />
                      )}
                    </Col>
                    <Col className="toggle-switch mt-3 pl-5">
                      <Switch
                        checkedChildren="Bar Chart"
                        unCheckedChildren="Pie Chart"
                        disabled
                      />
                    </Col>
                  </Col>
                ) : (
                  <Col></Col>
                )}
              </Row>
            </Col>
            <Col span={24} className="gutter-row mt-4 mb-3">
              <h6>{SCHEDULE}</h6>

              <FloatLabel label={SELECT_START_DATE} value="sDate">
                <Form.Item
                  name="sDate"
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={8} className="gutter-row">
                      <DatePicker
                        className="w-100"
                        name="sDate"
                        className="required"
                        disabled
                        value={poll.pollStartDate}
                      />
                    </Col>
                    <Col span="24" lg={8}>
                      <TimePicker value={poll.pollStartTime} disabled />
                    </Col>
                    <Col span="24" lg={8}>
                      <FloatLabel label="Time Zone" value={poll.timezone1}>
                        <Form.Item className="required mb-0" name="timezone1">
                          <Select
                            className=""
                            htmlFor="timezone1"
                            disabled
                            defaultValue={poll.timezone1}
                          ></Select>
                        </Form.Item>
                      </FloatLabel>
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
              <FloatLabel label={SELECT_END_DATE} value="eDate">
                <Form.Item name="eDate">
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                    <Col span="24" lg={8} className="gutter-row">
                      <DatePicker
                        className="w-100"
                        name="eDate"
                        disabled
                        value={poll.pollEndDate}
                      />
                    </Col>
                    <Col span="24" lg={8}>
                      <TimePicker value={poll.pollEndTime} disabled />
                    </Col>
                    <Col span="24" lg={8}>
                      <FloatLabel label={TIME_ZONE} value={poll.timezone2}>
                        <Form.Item className="mb-0" name="timezone2">
                          <Select
                            className=""
                            htmlFor="timezone2"
                            disabled
                            defaultValue={poll.timezone2}
                          ></Select>
                        </Form.Item>
                      </FloatLabel>
                    </Col>
                  </Row>
                </Form.Item>
              </FloatLabel>
            </Col>
          </Col>

          <div className="mt-5">
            <Button
              type="secondary"
              htmlType="submit"
              onClick={prev}
              className="mr-4"
            >
              {BACK}
            </Button>
            <Button type="primary" className="md" onClick={createPoll}>
              {PUBLISH}
            </Button>
          </div>
        </Form>
      </Col>
    </>
  );
};

export default CreatePollStepThree;
