import React from "react";
import {
  Layout,
  Row,
  Col,
  Input,
  Spin,
  notification,
  Form,
  Checkbox,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageEmp/manageEmp.scss";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined, PaperClipOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import PollPopover from "../table/PollPopover";
import authHeader from "../../services/auth-header";
import axios from "axios";
import momentUtil from "../../utils/momentUtil";
import { BASE_URL } from "../../userconfig";
import {
  CREATE_POLL,
  MANAGE_POLLS,
  SEARCH,
  SHOW_TIMEZONES,
} from "../../headings";
import {
  STATUS_CHANGED_NOTIFICATION_PART1,
  STATUS_CHANGED_NOTIFICATION_PART2,
  STATUS_CHANGED_NOTIFICATION_PART4,
  EXTEND_END_DATE_TEXT_PART3,
} from "../../messages";
import {
  COMPLETED,
  SCHEDULED,
  STATUS_ACTIVE,
  STATUS_DRAFT,
} from "../../values";
import PollFilterPopover from "./PollFilter";
import CugModal from "./../modal/ShowCugModal";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllPolls extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      cug: {},
      filterData: {},
      showTimezones: false,
    };
  }

  componentDidMount() {
    let { polls, cug, filter } = this.props;
    polls?.forEach((poll) => {
      poll.start_date = moment(poll.start_date)
        .utc()
        .local()
        .format("MM/DD/YYYY");
      poll.end_date = moment(poll.start_date).isValid()
        ? moment(poll.end_date).utc().local().format("MM/DD/YYYY")
        : "";
    });
    this.setState({ data: polls, cug: cug?.cug_field, filterData: filter });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.polls !== nextProps.polls) {
      nextProps.polls?.forEach((poll) => {
        poll.start_date = moment(poll.start_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
        poll.end_date = moment(poll.end_date)
          .utc()
          .local()
          .format("MM/DD/YYYY");
      });
      this.setState({ data: nextProps.polls });
    }
    if (this.props.cug !== nextProps.cug) {
      this.setState({ cug: nextProps.cug?.cug_field });
    }
    if (this.props.filter !== nextProps.filter) {
      this.setState({ filterData: nextProps.filter });
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  extendPollDate = (value, date) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    const data = {
      end_date: momentUtil.formatAsDDMMYYYY(date.endDate),
      end_time: momentUtil.formatAsHHmmss(date.endTime),
    };
    axios
      .put(BASE_URL + "extendEndDate/" + value.poll_id + "/", data, options)
      .then(() => {
        notification.config({
          placement: "bottomLeft",
        });
        notification.open({
          description: value.pollName + EXTEND_END_DATE_TEXT_PART3,
        });
        window.location = "/orgcc/pollsdirectory";
      });
  };

  onSearch = (value) => {
    const data = { ...this.state.filterData, search: value };
    this.setState({ filterData: data });
    this.props.setFilterOptions("search", data);
  };

  applyFilters = () => {
    this.props.setFilterOptions("filters", this.state.filterData);
  };

  reset = () => {
    this.setState({ filterData: {} });
    this.props.setFilterOptions("reset", {
      search: "",
      status_id: [],
      cug_filter: {},
    });
  };

  handlePollOnSelect = (value, type, item) => {
    var newArray = value.map((item) => {
      if (type === "status_id") {
        return parseInt(item);
      } else {
        return item;
      }
    });
    let data = {};
    if (item) {
      if (value.length === 0) {
        const cugData = this.state.filterData.cug_filter;
        const filteredCugData = Object.keys(cugData)
          .filter((key) => key !== item)
          .reduce((obj, key) => {
            obj[key] = cugData[key];
            return obj;
          }, {});
        data = {
          ...this.state.filterData,
          [type]: {
            ...filteredCugData,
          },
        };
      } else {
        data = {
          ...this.state.filterData,
          [type]: {
            ...this.state.filterData[type],
            [item]: newArray,
          },
        };
      }
    } else {
      data = { ...this.state.filterData, [type]: newArray };
    }
    this.setState({ filterData: data });
  };

  handleCheck = (e) => {
    if (e.target.checked) {
      this.setState({ showTimezones: true });
    } else {
      this.setState({ showTimezones: false });
    }
  };

  statusClicked = (record, via) => {
    let data = {};
    if (via.length === 0) {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description:
          STATUS_CHANGED_NOTIFICATION_PART1 +
          record.title +
          STATUS_CHANGED_NOTIFICATION_PART4,
      });
    } else if (via === "Scheduled" || via === "Active") {
      data = {
        status_id: 4,
      };
    } else if (via === "inActive") {
      const startDate = record.start_date;
      const sdate = startDate.split("/")[1];
      const smonth = startDate.split("/")[0];
      const syear = startDate.split("/")[2];
      const finalStartDate = new Date(smonth + "-" + sdate + "-" + syear);
      const currentDate = new Date();
      if (finalStartDate > currentDate) {
        data = {
          status_id: 7,
        };
      } else if (finalStartDate < currentDate) {
        data = {
          status_id: 5,
        };
      } else if (finalStartDate === currentDate) {
        const startTime = record.start_time;
        const SFullDate = smonth + "-" + sdate + "-" + syear;
        const startTimeWithDate = new Date(SFullDate + startTime);
        if (startTimeWithDate > new Date()) {
          data = {
            status_id: 7,
          };
        } else if (startTimeWithDate <= new Date()) {
          data = {
            status_id: 5,
          };
        }
      }
    }
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .put(BASE_URL + "changePollStatus/" + record.poll_id + "/", data, options)
      .then(() => {
        notification.config({
          placement: "bottomLeft",
        });
        notification.open({
          description:
            STATUS_CHANGED_NOTIFICATION_PART1 +
            record.title +
            STATUS_CHANGED_NOTIFICATION_PART2,
        });
        this.props.statusOnClicked();
      });
  };

  render() {
    const { loader } = this.props;
    let columns = [];
    if (this.state.cug && Object.keys(this.state.cug).length > 0) {
      columns = [
        {
          title: "",
          dataIndex: "",
          key: "name",
          width: "5%",
          render: (record) => {
            return {
              children:
                record.image || record.document ? <PaperClipOutlined /> : null,
              props: {
                "data-title": "",
              },
            };
          },
        },
        {
          title: "Poll Name",
          dataIndex: "title",
          key: "name",
          width: "22%",
          render: (name, record) => {
            return {
              children:
                record.poll_id != 2020202020 && record.poll_id != 1010101010 ? (
                  <Link
                    to={{
                      pathname: `/orgcc/editpoll/${record.poll_id}`,
                      state: record,
                    }}
                  >
                    <li>
                      <a href={`/orgcc/editpoll/${record.poll_id}`}>{name}</a>{" "}
                    </li>
                  </Link>
                ) : (
                  name
                ),
              props: {
                "data-title": "Poll Name",
              },
            };
          },
        },
        {
          title: "Start Date & Time",
          dataIndex: "",
          key: "start_date",
          width: "22%",
          render: (record) => {
            const startDate =
              record.start_date !== "Invalid date" ? record.start_date : "-";
            const sdate = record.start_date.split("/")[1];
            const smonth = record.start_date.split("/")[0];
            const syear = record.start_date.split("/")[2];
            const startTime = new Date(
              syear + "-" + smonth + "-" + sdate + "T" + record.start_time
            );
            return {
              children:
                startDate === "-"
                  ? startDate
                  : this.state.showTimezones &&
                    record.start_date_timezone &&
                    record.start_date_timezone.length > 0
                  ? startDate +
                    " " +
                    startTime.toLocaleString([], {
                      hour: "2-digit",
                      minute: "2-digit",
                    }) +
                    " " +
                    record.start_date_timezone
                  : startDate +
                    " " +
                    startTime.toLocaleString([], {
                      hour: "2-digit",
                      minute: "2-digit",
                    }),
              props: {
                "data-title": "Start Date & Time",
              },
            };
          },
        },
        {
          title: "End Date & Time",
          dataIndex: "",
          key: "end_date",
          width: "21%",
          render: (record) => {
            const endDate =
              record.end_date !== "Invalid date" ? record.end_date : "-";
            const edate = record.end_date.split("/")[1];
            const emonth = record.end_date.split("/")[0];
            const eyear = record.end_date.split("/")[2];
            const endTime = new Date(
              eyear + "-" + emonth + "-" + edate + "T" + record.end_time
            );
            return {
              children:
                endDate === "-"
                  ? endDate
                  : this.state.showTimezones && record.start_date_timezone
                  ? endDate + " " + endDate === "-"
                    ? null
                    : endTime.toLocaleString([], {
                        hour: "2-digit",
                        minute: "2-digit",
                      }) +
                      " " +
                      record.end_date_timezone
                  : endDate +
                    " " +
                    endTime.toLocaleString([], {
                      hour: "2-digit",
                      minute: "2-digit",
                    }),
              props: {
                "data-title": "Start Date & Time",
              },
            };
          },
        },
        {
          title: "CUG Details",
          dataIndex: "",
          key: "x",
          width: "20%",
          render: (selectedRows) => {
            return {
              children: (
                <div>
                  <CugModal selectedRows={selectedRows} />
                </div>
              ),
              props: {
                "data-title": "CUG Details",
              },
            };
          },
        },
        {
          title: "Status",
          dataIndex: "",
          key: "x",
          width: "10%",
          render: (record) => {
            return {
              children:
                record.poll_id != 1010101010 && record.poll_id != 2020202020 ? (
                  record.status_id === 4 ? (
                    <div className="secondary-tag-rounded tag">
                      {STATUS_DRAFT}
                    </div>
                  ) : record.status_id === 5 ? (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {STATUS_ACTIVE}
                    </div>
                  ) : record.status_id === 7 ? (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {SCHEDULED}
                    </div>
                  ) : (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {COMPLETED}
                    </div>
                  )
                ) : (
                  <div className="secondary-tag-rounded tag">{"Sample"}</div>
                ),
              props: {
                "data-title": "Status",
              },
            };
          },
        },
        {
          title: "",
          dataIndex: "",
          key: "x",
          width: "5%",
          render: (record) => {
            return {
              children: (
                <PollPopover
                  extendPollDate={this.extendPollDate}
                  data={record}
                  cugData={this.state.cug}
                />
              ),
              props: {
                "data-title": "Action",
              },
            };
          },
        },
      ];
    } else {
      columns = [
        {
          title: "",
          dataIndex: "",
          key: "name",
          width: "5%",
          render: (record) => {
            return {
              children:
                record.image || record.document ? <PaperClipOutlined /> : null,
              props: {
                "data-title": "",
              },
            };
          },
        },
        {
          title: "Poll Name",
          dataIndex: "title",
          key: "name",
          width: "22%",
          render: (name, record) => {
            return {
              children:
                record.poll_id !== 2020202020 &&
                record.poll_id !== 1010101010 ? (
                  <Link
                    to={{
                      pathname: `/orgcc/editpoll/${record.poll_id}`,
                      state: record,
                    }}
                  >
                    <li>
                      <a href={`/orgcc/editpoll/${record.poll_id}`}>{name}</a>{" "}
                    </li>
                  </Link>
                ) : (
                  name
                ),
              props: {
                "data-title": "Poll Name",
              },
            };
          },
        },
        {
          title: "Start Date & Time",
          dataIndex: "",
          key: "start_date",
          width: "22%",
          render: (record) => {
            const startDate =
              record.start_date !== null ? record.start_date : "-";
            const sdate = record.start_date.split("/")[1];
            const smonth = record.start_date.split("/")[0];
            const syear = record.start_date.split("/")[2];
            const startTime = new Date(
              syear + "-" + smonth + "-" + sdate + "T" + record.start_time
            );
            return {
              children:
                startDate === "-"
                  ? startDate
                  : this.state.showTimezones && record.start_date_timezone
                  ? startDate + " " + " " + startDate === "-"
                    ? null
                    : startTime.toLocaleString([], {
                        hour: "2-digit",
                        minute: "2-digit",
                      }) +
                      " " +
                      record.start_date_timezone
                  : startDate +
                    " " +
                    startTime.toLocaleString([], {
                      hour: "2-digit",
                      minute: "2-digit",
                    }),
              props: {
                "data-title": "Start Date & Time",
              },
            };
          },
        },
        {
          title: "End Date & Time",
          dataIndex: "",
          key: "end_date",
          width: "21%",
          render: (record) => {
            const endDate = record.end_date !== null ? record.end_date : "-";
            const edate = record.end_date.split("/")[1];
            const emonth = record.end_date.split("/")[0];
            const eyear = record.end_date.split("/")[2];
            const endTime = new Date(
              eyear + "-" + emonth + "-" + edate + "T" + record.end_time
            );
            return {
              children:
                endDate === "-"
                  ? endDate
                  : this.state.showTimezones && record.start_date_timezone
                  ? endDate + " " + endDate === "-"
                    ? null
                    : endTime.toLocaleString([], {
                        hour: "2-digit",
                        minute: "2-digit",
                      }) +
                      " " +
                      record.end_date_timezone
                  : endDate +
                    " " +
                    endTime.toLocaleString([], {
                      hour: "2-digit",
                      minute: "2-digit",
                    }),
              props: {
                "data-title": "Start Date & Time",
              },
            };
          },
        },
        {
          title: "Status",
          dataIndex: "",
          key: "x",
          width: "10%",
          render: (record) => {
            return {
              children:
                record.poll_id != 1010101010 && record.poll_id != 2020202020 ? (
                  record.status_id === 4 ? (
                    <div className="secondary-tag-rounded tag">
                      {STATUS_DRAFT}
                    </div>
                  ) : record.status_id === 5 ? (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {STATUS_ACTIVE}
                    </div>
                  ) : record.status_id === 7 ? (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {SCHEDULED}
                    </div>
                  ) : (
                    <div className="primary-tag-rounded tag">
                      <span className="circle mr-1"></span>
                      {COMPLETED}
                    </div>
                  )
                ) : (
                  <div className="secondary-tag-rounded tag">{"Sample"}</div>
                ),
              props: {
                "data-title": "Status",
              },
            };
          },
        },

        {
          title: "",
          dataIndex: "",
          key: "x",
          width: "5%",
          render: (record) => {
            return {
              children: (
                <PollPopover
                  extendPollDate={this.extendPollDate}
                  data={record}
                />
              ),
              props: {
                "data-title": "Action",
              },
            };
          },
        },
      ];
    }
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>{MANAGE_POLLS}</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Form form={this.props.form}>
                            <Form.Item name="search" id="search">
                              <Search
                                name="search"
                                id="search"
                                placeholder={SEARCH}
                                onSearch={this.onSearch}
                                autoComplete="off"
                              />
                            </Form.Item>
                          </Form>
                        </Col>
                        <Col
                          span={24}
                          lg={9}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <PollFilterPopover
                            handlePollOnSelect={this.handlePollOnSelect}
                            cug={this.state.cug}
                            applyFilters={this.applyFilters}
                            reset={this.reset}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link
                        to={{
                          pathname: "/orgcc/createpoll",
                          state: this.state.cug,
                        }}
                      >
                        <Buttons value={CREATE_POLL} />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Spin spinning={loader} size="large">
                  <Checkbox
                    checked={this.state.showTimezones}
                    onChange={(e) => this.handleCheck(e)}
                  >
                    {SHOW_TIMEZONES}
                  </Checkbox>
                  <Grid
                    rowKey="poll_id"
                    columns={columns}
                    data={this.state.data}
                  />
                </Spin>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllPolls;
