import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { getPollAsync } from "../../actions/poll";
import getCUG from "../../actions/cug";
import ShowAllPollResults from "./ShowAllPollResults";

const PollResultsContainer = (props) => {
  const [loader, setLoader] = useState(false);
  const [filter, setFilter] = useState({
    search: "",
    status_id: [],
    cug_filter: {},
  });
  const [checked, setChecked] = useState(false);
  const [totalPolls, setTotalPolls] = useState([]);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getPollAsync(filter));
  }, [filter]);

  useEffect(() => {
    if (props.pollsdata === null) {
      setLoader(true);
    } else if (props.pollsdata !== null) {
      setLoader(false);
    }
    const polls = props.pollsdata !== null ? props.pollsdata : [];
    const isInactivePollPresent = filter?.status_id?.includes(4);
    if (
      filter.search.length > 0 ||
      (Object.values(filter?.status_id).length > 0 && !isInactivePollPresent)
    ) {
      setTotalPolls(polls);
    }
  }, [props.pollsdata]);

  const setFilterOptions = (value) => {
    setFilter({ ...filter, ...value });
  };

  const handleCheck = (data) => {
    setChecked(!checked);
    setFilter(data);
  };

  return (
    <ShowAllPollResults
      polls={totalPolls}
      data={props}
      cug={props.cug}
      loader={loader}
      filter={filter}
      checked={checked}
      setLoader={setLoader}
      setFilterOptions={setFilterOptions}
      handleCheck={handleCheck}
    />
  );
};

function mapStateToProps(state) {
  const { pollsdata } = state.poll;
  const { cug } = state.cug;
  return {
    pollsdata,
    cug,
  };
}

export default connect(mapStateToProps)(PollResultsContainer);
