import UserService from '../services/user.service';

import{
    BROADCAST_FAIL,
    BROADCAST_SUCCESS,
    SET_MESSAGE,
    BROADCAST_DETAILS_SUCCESS,
    BROADCAST_DETAILS_FAIL
    } from "./types";
    

    export const getBroadCastMessages=(filterStatus,search,sort)=>(dispatch)=>{
        return UserService.getBroadCastMessages(filterStatus,search,sort).then((data)=>{
            dispatch({
                type:BROADCAST_SUCCESS,
                payload:{broadcast:data.data},
                
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:BROADCAST_FAIL,
            });
            
            dispatch({
                type:SET_MESSAGE,
                payload:message
            });
            return Promise.reject();
        }
        );
    };

    export const getBroadCastMessagesDetailsByID=(Id)=>(dispatch)=>{
        return UserService.getBroadCastMessagesDetailsByID(Id).then((data)=>{
            dispatch({
                type:BROADCAST_DETAILS_SUCCESS,
                payload:{broadcastDetailsData:data.data.cug_fields},
            });    
            
            return Promise.resolve();
        }, 
        (error)=>{
            const message=
            (error.response&&
                error.response.data && error.response.data.message)||
                error.message||
                error.toString();
            dispatch({
                type:BROADCAST_DETAILS_FAIL,
                payload:{}
            });
            return Promise.reject();
        }
        );
    };

