import React, { useState } from "react";
import "antd/dist/antd.css";
import "./assets/scss/style.scss";
import "./assets/icons/icon-styles.css";
import { Provider } from "react-redux";
import Routes from "./routes";
import store from "./services/stores";
import IdleTimer from "react-idle-timer";
import { Modal } from "antd";
import AuthService from "../src/services/auth-service";
import * as AppStorage from "../src/utils/AppStorage";
import { ABOUT_TO_LOGOUT_TEXT, LOGGED_OUT_TEXT } from "./messages";
import { OK_TEXT_MODAL } from "./headings";
import axios from "axios";
import refreshAuthHeader from "./services/refreshAuthHeader";
import { BASE_URL } from "./userconfig";

function App() {
  const timeOutIdle = 1200000; // 20 minutes
  const timeOutLogout = 60000; // a minute
  var idleTimer = null;
  const [idleModal, showIdleModal] = useState(false);
  const [logoutModal, showLogoutModal] = useState(false);
  const [timedOut, setTimedOut] = useState(false);

  const onIdle = (e) => {
    if (AppStorage.get("user")) {
      const isTimedOut = timedOut;
      if (isTimedOut) {
        showIdleModal(false);
        showLogoutModal(true);
      } else {
        showIdleModal(true);
        idleTimer.reset();
        setTimedOut(true);
      }
    }
  };

  const handleIdleOk = () => {
    showIdleModal(false);
  };

  const handleIdleCancel = () => {
    showIdleModal(false);
  };

  const handleLogoutOk = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const handleLogoutCancel = () => {
    if (AppStorage.get("user")) {
      AuthService.logout();
    }
  };

  const onActive = (e) => {
    setTimedOut(false);
  };

  const onAction = (e) => {
    setTimedOut(false);
  };

  // response interceptor to refresh token on receiving token expired
  axios.interceptors.response.use(
    (response) => {
      return response;
    },
    function (error) {
      const originalRequest = error.config;
      const refreshToken = refreshAuthHeader();
      const user = JSON.parse(AppStorage.get("user"));
      if (
        refreshToken &&
        error.response.status === 401 &&
        !originalRequest._retry
      ) {
        originalRequest._retry = true;
        return axios
          .post(BASE_URL + "tokenRefresh/", {
            refresh: refreshToken,
          })
          .then((res) => {
            if (res.status === 200) {
              const updatedUserObject = {
                ...user,
                user_data: {
                  ...user.user_data,
                  tokens: {
                    ...user.user_data.tokens,
                    access: res.data.access,
                  },
                },
              };
              AppStorage.set("user", JSON.stringify(updatedUserObject));
              originalRequest.headers["Authorization"] =
                "Bearer " + res.data.access;
              // Resending original request
              return axios(originalRequest);
            }
          });
      }
      return Promise.reject(error);
    }
  );

  return (
    <Provider store={store}>
      <IdleTimer
        ref={(ref) => {
          idleTimer = ref;
        }}
        element={document}
        onIdle={onIdle}
        onAction={onAction}
        onActive={onActive}
        debounce={250}
        timeout={!timedOut ? timeOutIdle : timeOutLogout}
      />
      <div className="App">
        <div>
          <div className="content">
            <Modal
              visible={idleModal}
              onOk={handleIdleOk}
              onCancel={handleIdleCancel}
              centered
              width={350}
              okButtonProps={{
                children: "Custom OK",
              }}
              cancelButtonProps={{ style: { display: "none" } }}
              okText={OK_TEXT_MODAL}
            >
              <p>{ABOUT_TO_LOGOUT_TEXT}</p>
            </Modal>
            <Modal
              visible={logoutModal}
              onOk={handleLogoutOk}
              onCancel={handleLogoutCancel}
              centered
              width={350}
              okButtonProps={{
                children: "Custom OK",
              }}
              cancelButtonProps={{ style: { display: "none" } }}
              okText={OK_TEXT_MODAL}
            >
              <p>{LOGGED_OUT_TEXT}</p>
            </Modal>
            <Routes />
          </div>
        </div>
      </div>
    </Provider>
  );
}

export default App;
