import React from "react";
import { TreeSelect } from "antd";
import { SELECT } from "../../labels";
const { SHOW_PARENT } = TreeSelect;

class MultiSelect extends React.Component {
  state = {
    value: undefined,
    treeData: [],
  };

  componentDidMount() {
    let flag = 0;
    let stateValues = [];
    if (this.props.pollCug) {
      Object.entries(this.props.pollCug)?.map((value) => {
        if (Object.values(value[1])?.length > 0) {
          flag = 1;
          if (value[0] === this.props.item) {
            stateValues = Object.values(value[1]);
          }
        }
      });
    }
    this.setState({
      value: flag === 1 ? stateValues : undefined,
    });
    if (!this.props.viaStep3) {
      const treeValues = [];
      this.props.cugData[this.props.item]?.map((value) => {
        treeValues.push({
          title: value,
          value: value,
          key: value,
        });
      });
      this.setState({
        treeData: treeValues,
      });
    }
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.itemRemoved !== prevProps.itemRemoved &&
      this.props.item === this.props.itemRemoved
    ) {
      this.setState({ value: undefined });
    }
  }

  onChange = (value) => {
    this.setState({ value });
    this.props.handleOnCUGSelect(this.props.item, value);
  };

  render() {
    const placeholder =
      SELECT +
      `${this.props.item.substring(
        this.props.item.indexOf("_") + 1,
        this.props.item.length
      )}`;
    const tProps = {
      disabled:
        this.props.viaStep3 ||
        this.props.pollStatusId === 5 ||
        this.props.pollStatusId === 8,
      treeData: this.state.treeData,
      value: this.state.value,
      onChange: this.onChange,
      treeCheckable: true,
      placeholder: placeholder,
      showCheckedStrategy: SHOW_PARENT,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiSelect;
