import { CUG_SUCCESS, CUG_FAILURE } from "./types";
import UserService from "../services/user.service";

const getCUG = () => (dispatch) => {
  return UserService.getCUGValues().then(
    (response) => {
      dispatch({
        type: CUG_SUCCESS,
        payload: {
          cug: response.data,
        },
      });
      return Promise.resolve();
    },
    (error) => {
      dispatch({
        type: CUG_FAILURE,
      });
      return Promise.reject();
    }
  );
};
export default getCUG;
