import React, { useState, useEffect } from "react";
import { Layout, Row, Steps, Form, Breadcrumb, message, Spin } from "antd";
import { NavLink, useHistory, useLocation } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import EditPollStepOne from "../form/EditPollStepOne";
import EditPollStepTwo from "../form/EditPollStepTwo";
import EditPollStepThree from "../form/EditPollStepThree";
import momentUtil from "../../utils/momentUtil";
import authHeader from "../../services/auth-header";
import axios from "axios";
import moment from "moment";
import { connect } from "react-redux";
import getCUG from "../../actions/cug";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import { EDIT_POLL, POLLS, VIEW_POLL } from "../../headings";
import {
  POLL_CREATE_STEP1,
  POLL_CREATE_STEP2,
  POLL_CREATE_STEP3,
} from "../../values";

const { Content } = Layout;
const { Step } = Steps;

const steps = [
  {
    title: POLL_CREATE_STEP1,
    content: "First-content",
  },
  {
    title: POLL_CREATE_STEP2,
    content: "Second-content",
  },
  {
    title: POLL_CREATE_STEP3,
    content: "Last-content",
  },
];

var CryptoJS = require("crypto-js");
const EditPollStepper = (props) => {
  const history = useHistory();
  const [poll, setPoll] = useState({
    pollName: "",
    pollHelpText: "",
    pollDocument: [],
    showResult: false,
    pollChartType: "",
    pollQuestion: "",
    pollQuestionType: "",
    pollRatingType: "",
    pollRatingResponse: 0,
    pollStartDate: "",
    pollEndDate: "",
    pollStartTime: "",
    pollEndTime: "",
    pollResponse: [],
    pollOptionText: [],
    pollId: "",
    cugData: {},
    cugDetails: {},
    status_id: "",
    switch: false,
    timezone1: "",
    timezone2: "",
    pastStartTimeError: false,
    pastEndTimeError: false,
  });
  const [loader, setLoader] = useState(false);
  const [current, setCurrent] = useState(0);
  const [pollError, setpollError] = useState("");
  const [chartError, showChartError] = useState(false);
  const [uploadError, showuploadError] = useState(false);
  const [isImage, setIsImage] = useState(false);
  const [attachmentName, setAttachmentName] = useState("");
  const [form] = Form.useForm();
  let location = useLocation();
  const [optionsError, setOptionsError] = useState(false);
  const pollID = props.computedMatch?.params.id;

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    axios.get(BASE_URL + `getPoll/${pollID}/`, options).then((res) => {
      const data = res.data;
      const sdate = momentUtil.formatAsDDMMYYYY(data.start_date).split("-")[0];
      const smonth = momentUtil.formatAsDDMMYYYY(data.start_date).split("-")[1];
      const syear = momentUtil.formatAsDDMMYYYY(data.start_date).split("-")[2];
      const edate = momentUtil.formatAsDDMMYYYY(data.end_date).split("-")[0];
      const emonth = momentUtil.formatAsDDMMYYYY(data.end_date).split("-")[1];
      const eyear = momentUtil.formatAsDDMMYYYY(data.end_date).split("-")[2];
      let cugData = {};
      if (props.cug && props.cug !== null) {
        Object.keys(props.cug?.cug_field).forEach((value) => {
          if (data.cug_fields) {
            Object.entries(data.cug_fields).forEach((entry) => {
              if (entry[0] === value) {
                cugData = {
                  ...cugData,
                  [value]: entry[1],
                };
              }
            });
          }
        });
      }
      setPoll({
        ...poll,
        pollId: data.poll_id,
        pollName: data.title,
        pollHelpText: data.help_text,
        pollDocument:
          data.image !== null || data.document !== null
            ? [
                {
                  uid: "1",
                  name:
                    data.image_name !== null
                      ? data.image_name
                      : data.document_name !== null
                      ? data.document_name
                      : "",
                  status: "done",
                  url: data.image ? data.image : data.document,
                },
              ]
            : "",
        showResult: data.show_result_to_users,
        pollChartType: data.poll_result_chart_type.toString(),
        pollQuestion: data.question,
        pollQuestionType: data.poll_question_type.toString(),
        pollRatingType: data.poll_question_rating_type
          ? data.poll_question_rating_type.toString()
          : "",
        pollRatingResponse: data.poll_question_rating_type,
        pollStartDate: moment(
          momentUtil.formatAsDDMMYYYY(data.start_date),
          "DD-MM-YYYY"
        ),
        pollEndDate: moment(
          momentUtil.formatAsDDMMYYYY(data.end_date),
          "DD-MM-YYYY"
        ),
        pollStartTime: new Date(
          syear + "-" + smonth + "-" + sdate + "T" + data.start_time
        ),
        timezone1: data.start_date_timezone,
        pollEndTime: new Date(
          eyear + "-" + emonth + "-" + edate + "T" + data.end_time
        ),
        timezone2: data.end_date_timezone,
        status_id: data.status_id,
        cugData: cugData,
        cugDetails: data.cug_fields,
        pollResponse: data.option_text.map((value) => value.option_text),
        pollOptionText: data.option_text,
      });
      setAttachmentName(
        data.image_name !== null
          ? data.image_name
          : data.document_name !== null
          ? data.document_name
          : ""
      );
      const isImage =
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("jpg") ||
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("jpeg") ||
        data.image
          ?.slice(data.image.lastIndexOf("."), data.image.length)
          .includes("png");
      if (isImage) {
        setIsImage(true);
      } else {
        setIsImage(false);
      }
      setLoader(false);
      form.setFieldsValue({
        pollName: data.title,
        pollHelpText: data.help_text,
        pollQuestion: data.question,
      });
    });
  }, [props.cug]);

  const onChange = (checked, e) => {
    setPoll({ ...poll, [e.target.id]: checked });
  };

  const handleOnSelect = (name, value) => {
    setPoll({ ...poll, [name]: value });
  };

  const handleOnCUGSelect = (name, value) => {
    setPoll({
      ...poll,
      cugDetails: {
        ...poll.cugDetails,
        [name]: [...value],
      },
      cugData: {
        ...poll.cugData,
        [name]: [...value],
      },
    });
  };

  const itemCleared = (value) => {
    const filteredCugData = Object.keys(poll.cugDetails).reduce((obj, key) => {
      if (key === value) {
        obj[key] = [];
      } else {
        obj[key] = poll.cugDetails[key];
      }
      return obj;
    }, {});
    setPoll({ ...poll, cugDetails: filteredCugData, cugData: filteredCugData });
  };

  const onSwitchChange = (checked, e) => {
    setPoll({ ...poll, switch: checked });
  };

  const onCancel = () => {
    history.push("/orgcc/pollsdirectory");
  };

  const onCreateChange = (e) => {
    setPoll({ ...poll, [e.target.id]: e.target.value });
  };

  const checkChartType = (value) => {
    if (value === "3" && poll.pollChartType != "4") {
      showChartError(true);
    }
  };

  const handlePollOnSelect = (value, name) => {
    if (value === "3" && name === "pollQuestionType") {
      checkChartType(value);
    } else {
      showChartError(false);
    }
    if (name === "pollRatingType") {
      setPoll({
        ...poll,
        [name]: value,
        ["pollResponse"]: ["1", "2", "3", "4", "5"],
      });
    } else {
      setPoll({ ...poll, [name]: value });
    }
  };

  const setPollsDate = (name, date) => {
    setPoll({ ...poll, [name]: date });
  };

  const setSingleChoiceResponse = (value) => {
    let name = "pollResponse";
    setPoll({ ...poll, [name]: value });
  };

  const handleStepOneUpload = (value, name) => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    } else {
      if (value.file.status === "removed") {
        showuploadError(false);
      }
      let fileList = value.fileList.slice(-1);
      checkDoucmentType(value.file.type);
      fileList.forEach(function (file, index) {
        let reader = new FileReader();
        reader.onload = (e) => {
          file.base64 = e.target.result;
          file.response = "Uploaded successfully";
        };
        reader.readAsDataURL(file.originFileObj);
      });
      setAttachmentName(value.file.name);
      setPoll({ ...poll, [name]: fileList });
    }
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType?.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handlePollImagePreview = async (file) => {
    if (
      file.name?.includes("jpeg") ||
      file.name?.includes("jpg") ||
      file.name?.includes("png") ||
      file.name?.includes("gif") ||
      file.name?.includes("jfif") ||
      file.name?.includes("tiff")
    ) {
      file.preview = await getBase64(file.originFileObj);
      setPoll({
        ...poll,
        previewImage: file.url || file.preview,
        previewVisible: true,
        previewTitle:
          file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
      });
    } else {
      return false;
    }
  };

  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const handleCancel = (value) => setPoll({ ...poll, previewVisible: false });

  function getBase64(file) {
    var atb = btoa(file);
    var base64Blob = new Blob([atb]);
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(base64Blob);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  }

  function convertImgToBase64(url, callback, outputFormat) {
    var canvas = document.createElement("CANVAS");
    var ctx = canvas.getContext("2d");
    var img = new Image();
    img.crossOrigin = "Anonymous";
    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0);
      var dataURL = canvas.toDataURL(outputFormat || "image/png");
      callback.call(this, dataURL);
      // Clean up
      canvas = null;
    };
    img.src = url;
  }

  const handleOnRemove = () => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    } else {
      setPoll({ ...poll, pollImageValidation: true });
    }
  };

  const handleBeforeUpload = (file, name) => {
    if (poll.status_id === 8 || poll.status_id === 5) {
      return null;
    } else {
      let xlsxfileExt = file.name.includes(".xls");
      let pptxfileExt = file.name.includes(".pptx");
      let docfileExt = file.name.includes(".doc");
      let csvfileExt = file.name.includes(".csv");

      const isValidDoc =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif" ||
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;
      if (!isValidDoc) {
        showuploadError(true);
        message.error(`${file.name} is not a supported format file type.`);
      } else {
        showuploadError(false);
      }
      const isImage =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif";
      const isDocument =
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;
      const isLt5M = file.size / 1024 / 1024 < 5;
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (isImage) {
        if (!isLt5M && isImage) {
          showuploadError(true);
          message.error("Image must smaller than 5MB!");
        } else {
          showuploadError(false);
        }
      }

      if (isDocument) {
        if (!isLt10M && isDocument) {
          showuploadError(true);
          message.error("Document must smaller than 10MB!");
        } else {
          showuploadError(false);
        }
      }
      return (isValidDoc && isLt5M) || isLt10M;
    }
  };

  const editPoll = () => {
    setLoader(true);
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let optionsData = [];
    poll.pollResponse.map((value) => optionsData.push({ option_text: value }));

    const finalOptions = optionsData.map((value) => {
      var option = Object.assign({}, value);
      poll.pollOptionText.map((item) => {
        if (item.option_text === value.option_text) {
          option.poll_option_id = item.poll_option_id;
          option.poll = poll.pollId;
        } else if (
          item.option_text !== value.option_text &&
          !option.poll_option_id
        ) {
          option.poll_option_id = "";
          option.poll = poll.pollId;
        }
      });
      return option;
    });
    let data = {};

    const startDate = moment.isMoment(poll.pollStartDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollStartDate)
      : poll.pollStartDate;
    const endDate = moment.isMoment(poll.pollEndDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollEndDate)
      : poll.pollEndDate;
    const startTime =
      poll.pollStartTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollStartTime)
        : poll.pollStartTime;
    const endTime =
      poll.pollEndTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollEndTime)
        : poll.pollEndTime;
    if (
      poll.pollDocument?.length === 0 ||
      poll.pollDocument[0].url?.length === 0
    ) {
      data = {
        update_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          poll_question_type: parseInt(poll.pollQuestionType),
          poll_question_rating_type: poll.pollRatingType
            ? parseInt(poll.pollRatingType)
            : "",
          question: poll.pollQuestion,
          start_date: startDate.length > 0 ? startDate : null,
          start_time: startTime.length > 0 ? startTime : null,
          start_date_timezone: poll.timezone1.length > 0 ? poll.timezone1 : "",
          end_date: endDate.length > 0 ? endDate : null,
          end_time: endTime.length > 0 ? endTime : null,
          end_date_timezone: poll.timezone2.length > 0 ? poll.timezone2 : "",
          status_id: poll.status_id,
          poll_id: poll.pollId,
          cug_filters: poll.cugData,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: finalOptions,
      };
      axios
        .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
        .then(() => {
          setLoader(false);
          window.location = "/orgcc/pollsdirectory";
        });
    } else {
      if (poll.pollDocument[0]?.url) {
        if (isImage) {
          convertImgToBase64(poll.pollDocument[0]?.url, function (base64Img) {
            data = {
              update_poll: {
                title: poll.pollName,
                help_text: poll.pollHelpText,
                image: base64Img,
                image_name: attachmentName,
                show_result_to_users: poll.showResult ? 1 : 0,
                poll_result_chart_type: poll.pollChartType
                  ? parseInt(poll.pollChartType)
                  : 2,
                poll_question_type: parseInt(poll.pollQuestionType),
                poll_question_rating_type: poll.pollRatingType
                  ? parseInt(poll.pollRatingType)
                  : "",
                question: poll.pollQuestion,
                start_date: startDate.length > 0 ? startDate : null,
                start_time: startTime.length > 0 ? startTime : null,
                start_date_timezone:
                  poll.timezone1.length > 0 ? poll.timezone1 : "",
                end_date: endDate.length > 0 ? endDate : null,
                end_time: endTime.length > 0 ? endTime : null,
                end_date_timezone:
                  poll.timezone2.length > 0 ? poll.timezone2 : "",
                status_id: poll.status_id,
                poll_id: poll.pollId,
                cug_filters: poll.cugData,
                created_by: parseInt(
                  CryptoJS.AES.decrypt(
                    user.user_details.org_user_id,
                    "anonymoose"
                  ).toString(CryptoJS.enc.Utf8)
                ),
              },
              option: finalOptions,
            };
            axios
              .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
              .then(() => {
                setLoader(false);
                window.location = "/orgcc/pollsdirectory";
              });
          });
        } else {
          getBase64(poll.pollDocument[0]?.url).then((res) => {
            data = {
              update_poll: {
                title: poll.pollName,
                help_text: poll.pollHelpText,
                document: res,
                document_name: attachmentName,
                show_result_to_users: poll.showResult ? 1 : 0,
                poll_result_chart_type: poll.pollChartType
                  ? parseInt(poll.pollChartType)
                  : 2,
                poll_question_type: parseInt(poll.pollQuestionType),
                poll_question_rating_type: poll.pollRatingType
                  ? parseInt(poll.pollRatingType)
                  : "",
                question: poll.pollQuestion,
                start_date: startDate.length > 0 ? startDate : null,
                start_time: startTime.length > 0 ? startTime : null,
                start_date_timezone:
                  poll.timezone1.length > 0 ? poll.timezone1 : "",
                end_date: endDate.length > 0 ? endDate : null,
                end_time: endTime.length > 0 ? endTime : null,
                end_date_timezone:
                  poll.timezone2.length > 0 ? poll.timezone2 : "",
                status_id: poll.status_id,
                poll_id: poll.pollId,
                cug_filters: poll.cugData,
                created_by: parseInt(
                  CryptoJS.AES.decrypt(
                    user.user_details.org_user_id,
                    "anonymoose"
                  ).toString(CryptoJS.enc.Utf8)
                ),
              },
              option: finalOptions,
            };
            axios
              .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
              .then(() => {
                setLoader(false);
                window.location = "/orgcc/pollsdirectory";
              });
          });
        }
      } else {
        if (isImage) {
          data = {
            update_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              image: poll.pollDocument.base64,
              image_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type: poll.pollRatingType
                ? parseInt(poll.pollRatingType)
                : "",
              question: poll.pollQuestion,
              start_date: startDate.length > 0 ? startDate : null,
              start_time: startTime.length > 0 ? startTime : null,
              start_date_timezone:
                poll.timezone1.length > 0 ? poll.timezone1 : "",
              end_date: endDate.length > 0 ? endDate : null,
              end_time: endTime.length > 0 ? endTime : null,
              end_date_timezone:
                poll.timezone2.length > 0 ? poll.timezone2 : "",
              status_id: poll.status_id,
              poll_id: poll.pollId,
              cug_filters: poll.cugData,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        } else {
          data = {
            update_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              document: poll.pollDocument.base64,
              document_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type: poll.pollRatingType
                ? parseInt(poll.pollRatingType)
                : "",
              question: poll.pollQuestion,
              start_date: startDate.length > 0 ? startDate : null,
              start_time: startTime.length > 0 ? startTime : null,
              start_date_timezone:
                poll.timezone1.length > 0 ? poll.timezone1 : "",
              end_date: endDate.length > 0 ? endDate : null,
              end_time: endTime.length > 0 ? endTime : null,
              end_date_timezone:
                poll.timezone2.length > 0 ? poll.timezone2 : "",
              status_id: poll.status_id,
              poll_id: poll.pollId,
              cug_filters: poll.cugData,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        }
        axios
          .put(BASE_URL + "poll/" + poll.pollId + "/", data, options)
          .then(() => {
            setLoader(false);
            window.location = "/orgcc/pollsdirectory";
          });
      }
    }
  };

  return (
    <React.Fragment>
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content>
              <Spin spinning={loader} size="large">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/pollsdirectory">
                      <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>
                      {poll.status_id === 8 || poll.status_id === 5
                        ? VIEW_POLL
                        : EDIT_POLL}
                    </Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">
                    {poll.status_id === 8 || poll.status_id === 5
                      ? VIEW_POLL
                      : EDIT_POLL}
                  </h1>
                </Row>
                <Steps className="mt-2" current={current}>
                  {steps.map((item) => (
                    <Step key={item.title} title={item.title} />
                  ))}
                </Steps>
                <div className="mid-content">
                  <Row>
                    {current === 0 ? (
                      <EditPollStepOne
                        next={next}
                        poll={poll}
                        form={form}
                        setOptionsError={setOptionsError}
                        pollError={pollError}
                        chartError={chartError}
                        handlePollOnSelect={handlePollOnSelect}
                        setSingleChoiceResponse={setSingleChoiceResponse}
                        onCreateChange={onCreateChange}
                        setpollError={setpollError}
                        optionsError={optionsError}
                        location={location}
                        editPoll={editPoll}
                      />
                    ) : current === 1 ? (
                      <EditPollStepTwo
                        next={next}
                        prev={prev}
                        poll={poll}
                        uploadError={uploadError}
                        setPollsDate={setPollsDate}
                        handleStepOneUpload={handleStepOneUpload}
                        handleBeforeUpload={handleBeforeUpload}
                        handleOnRemove={handleOnRemove}
                        handlePollImagePreview={handlePollImagePreview}
                        handleCancel={handleCancel}
                        onChange={onChange}
                        setPoll={setPoll}
                        cug={props.cug}
                        onSwitchChange={onSwitchChange}
                        handleOnSelect={handleOnSelect}
                        handleOnCUGSelect={handleOnCUGSelect}
                        itemCleared={itemCleared}
                        editPoll={editPoll}
                      />
                    ) : current === 2 ? (
                      <EditPollStepThree
                        prev={prev}
                        poll={poll}
                        handlePollImagePreview={handlePollImagePreview}
                        handleCancel={handleCancel}
                        editPoll={editPoll}
                      />
                    ) : (
                      ""
                    )}
                  </Row>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};

function mapStateToProps(state) {
  const { cug } = state.cug;
  return {
    cug,
  };
}

export default connect(mapStateToProps)(EditPollStepper);
