import React from "react";
import { Tag, Radio, Checkbox } from "antd";
import styles from "./managePoll.module.scss";

const ShowChoices = (props) =>
  props.pollResponse.map((value, key) => {
    const isLongTag = value.length > 20;
    return (
      <>
        {props.questionType === "1" ? (
          <Radio value={key} disabled></Radio>
        ) : (
          <Checkbox disabled></Checkbox>
        )}
        <Tag key={key} closable={false} className={`${styles.edit_tag}`}>
          <span>{isLongTag ? `${value.slice(0, 20)}...` : value}</span>
        </Tag>
      </>
    );
  });

export default ShowChoices;
