import React from "react";
import { TreeSelect } from "antd";
import {
  MAKE_APP_USER,
  MAKE_SUPER_USER,
  MAKE_CONTROL_CENTER,
  MAKE_IDB_USER,
} from "../../values";
import { SELECT_ROLE } from "../../labels";

const { SHOW_PARENT } = TreeSelect;
class MultiRoleSelection extends React.Component {
  state = {
    treeData: [],
    value: [],
  };

  componentDidMount() {
    const statusTreeData = [
      {
        title: MAKE_APP_USER,
        value: "0",
        key: "0",
        disabled: true,
      },
      {
        title: MAKE_SUPER_USER,
        value: "1",
        key: "1",
      },
      {
        title: MAKE_CONTROL_CENTER,
        value: "2",
        key: "2",
      },
      {
        title: MAKE_IDB_USER,
        value: "3",
        key: "3",
      },
    ];
    this.setState({
      treeData: statusTreeData,
    });
  }

  componentDidUpdate(prevProps) {
    if (this.props !== prevProps) {
      let stateValues = [];
      if (this.props.viaCreate) {
        this.setState({
          value: this.props.roles,
        });
      } else {
        Object.entries(this.props.roles)?.map((value) => {
          if (value[1] === "1") {
            stateValues.push(value[0]);
          }
        });
        this.setState({
          value: stateValues,
        });
      }
    }
  }

  render() {
    const tProps = {
      value: this.state.value,
      treeData: this.state.treeData,
      onChange: this.props.handleRoleOnSelect,
      treeCheckable: true,
      showCheckedStrategy: SHOW_PARENT,
      placeholder: SELECT_ROLE,
      style: {
        width: "100%",
      },
    };
    return <TreeSelect {...tProps} />;
  }
}

export default MultiRoleSelection;
